import _ from 'lodash'
import api from 'api'
import { Account, config } from 'librarys/lightcone_v3';
import { notification } from 'antd';
import utils from 'utils'

const initState = {
  list: [],
  cond: {
    token: undefined,
    page: 1,
    pageSize: 100,
    statuses: undefined,
    types: undefined,
    start: undefined,
    end: undefined,
  },
}


async function checkAllowance (tokenSymbol, depositAmout) {
  let owner = window.wallet.account
  if (tokenSymbol.toUpperCase() === 'ETH') {
    return 0
  } else {
    const allowance = await api.getAllowance(owner, tokenSymbol);
    const amount = Number(
      config.fromWEI(
        tokenSymbol,
        allowance,
        config.getTokenBySymbol(tokenSymbol).precision
      )
    );
    if (Number(depositAmout) > amount) {
      const txCount = amount === 0 ? 1 : 2;
      return txCount
    }
  }
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
    updateFilterCond: (state, payload) => {
      let cond = { ...state.cond, ...payload }
      return {
        ...state,
        cond
      }
    },
  },
  effects: dispatch => ({
    async fetchTransactions (payload, { dexAccount, transfer }) {
      let { accountId, apiKey } = dexAccount
      if (!accountId || !apiKey) return

      let { offset, limit } = utils.formatPageParams(transfer.cond)

      let transactions = await api.getTransactions({
        accountId,
        apiKey,
        offset,
        limit,
        statuses: transfer.cond.statuses,
        types: transfer.cond.types,
        start: transfer.cond.start,
        end: transfer.cond.end,
        symbol: transfer.cond.token
      });
      if (!transactions) return

      // console.info("########", {transactions})
      this.update({ list: transactions })
    },
    // approveTxCount(只是枚举类型): 0 代表授权额充足 1 代表授权额不足但是当前授权额是0， 2 代表授权额不足，并且授权额不为零
    // 这个是因为ERC20规范，授权额不能直接增加，必须先改成0
    async deposit ({ symbol, amount, gasPrice }, { dexAccount }) {
      const approveTxCount = await checkAllowance(symbol, amount)

      // Gas price
      let nonce = await api.getNonce(window.wallet.account);
      let exchangeAccount = new Account(window.wallet);

      if (approveTxCount === 2) {
        const signedEthereumTx = await exchangeAccount.approveZero(
          symbol,
          nonce,
          gasPrice
        );
        console.log('Received approve to  signEthereumTx');
        console.log(signedEthereumTx);
        await api.sendApprove(signedEthereumTx);
        nonce += 1;
      }

      // Approve
      if (approveTxCount !== 0) {
        const signedEthereumTx = await exchangeAccount.approveMax(
          symbol,
          nonce,
          gasPrice
        );
        console.log('Received approve to  signEthereumTx');
        console.log(signedEthereumTx);
        await api.sendApprove(signedEthereumTx);
        nonce += 1;
      }

      const signedEthereumTx = await exchangeAccount.depositTo(
        symbol,
        amount,
        nonce,
        gasPrice
      );
      console.log('Received deposit signEthereumTx');
      console.log(signedEthereumTx);
      const response = await api.sendDeposit(signedEthereumTx);
      const data = response['result'];
      console.log('Response of deposit: ', data);

      // await this.props.fetchMyBalances(
      //   this.props.dexAccount.account.accountId,
      //   this.props.dexAccount.account.apiKey
      // );

      // TODO: test
      // dispatch.balance.fetchBalances()

      if (data) {
        notification['success']({
          duration: 5,
          message: '提交成功，需要30个区块确认，大约需要10分钟，请耐心等待。'
        })
        return true
      } else {
        return false
      }
    },
    async withdraw ({ symbol, amount, gasPrice }, { dexAccount }) {
      const nonce = await api.getNonce(window.wallet.account);
      let exchangeAccount = new Account(window.wallet);
      const signedEthereumTx = await exchangeAccount.onchainWithdrawal(
        symbol,
        amount,
        nonce,
        gasPrice
      );

      console.log('Received withdraw signEthereumTx');
      console.log(signedEthereumTx);
      const response = await api.sendWithdrawTransaction(signedEthereumTx);
      const data = response['result'];
      console.log('Response of withdraw: ', data);

      // await this.props.fetchMyBalances(
      //   this.props.dexAccount.account.accountId,
      //   this.props.dexAccount.account.apiKey
      // );

      // TODO: test
      // dispatch.balance.fetchBalances()

      if (data) {
        notification['success']({
          duration: 5,
          message: '提交成功，需要30个区块确认，大约需要40-60分钟，请耐心等待。'
        })
        return true
      } else {
        return false
      }
    }
  })
}