import _ from 'lodash'
import numeral from 'numeral'
import { config } from 'librarys/lightcone_v3'
import api from 'api'

const initState = {
  CNY: [],
  USD: [],
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload })
  },
  effects: dispatch => ({
    async fetchRate (payload, { dexAccount }) {
      let { apiKey } = dexAccount

      var CNYRates = await api.getPrice({
        legal: 'CNY',
        apiKey
      })
      let usdt = _.find(CNYRates, {symbol: 'USD'})
      CNYRates.push({
        ...usdt,
        symbol: 'USDT'
      })

      var USDRates = await api.getPrice({
        legal: 'USD',
        apiKey
      })
      usdt = _.find(USDRates, {symbol: 'USD'})
      CNYRates.push({
        ...usdt,
        symbol: 'USDT'
      })

      this.update({
        CNY: CNYRates,
        USD: USDRates,
      })
    }
  })
}