import numeral from 'numeral';
import _ from 'lodash'
import moment from 'moment'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { Pagination } from 'components';
import { config } from 'librarys/lightcone_v3';
import { Table, Select, DatePicker } from 'antd';
import utils from 'utils'
import api from 'api'
import { ENUM_ORDER_STATUS } from 'utils/enum'
const { Option } = Select;
const { RangePicker } = DatePicker;

const columns = [
  {
    align: 'left',
    title: `时间`,
    dataIndex: 'createdAt',
    width: 90,
    render: (unused, row) => {
      return (<Text>{moment(row.createdAt).format('MM-DD HH:mm')}</Text>)
    }
  },
  {
    align: 'left',
    title: `交易对`,
    dataIndex: 'market',
    width: 100,
    render: (market, row) => {
      let [baseSymbol, quoteSymbol] = market.split('-')
      return (<Flex>{baseSymbol}<Text color="hint">/{quoteSymbol}</Text></Flex>)
    }
  },
  {
    align: 'left',
    title: '类型',
    dataIndex: 'side',
    width: 30,
    render: (unused, row) => {
      return (<Text>{row.side === 'BUY' ? "买入" : "卖出"}</Text>)
    }
  },
  {
    align: 'right',
    title: `数量`,
    dataIndex: 'filledSize',
    width: 100,
    render: (filledSize, row) => {
      let isBuy = (row.side === 'BUY')
      return (
        <Text color={isBuy ? "success" : "failure"}>{isBuy ? '+' : '-'}{filledSize}</Text>
      )
    }
  },
  {
    align: 'right',
    title: `价格`,
    dataIndex: 'price',
    width: 100,
    render: (price, row) => (
      <Text>{price}</Text>
    )
  },
  {
    align: 'right',
    title: `成交金额`,
    dataIndex: 'volume',
    width: 120,
    render: volume => (
      <Text>{volume}</Text>
    )
  },
  // {
  //   align: 'right',
  //   title: `已成交`,
  //   dataIndex: 'filledSize',
  //   width: 180,
  //   render: (filledSize, row) => (
  //     <Flex alignItems="center" justifyContent="flex-end">
  //       <Text mr={'4px'}>{filledSize}</Text>
  //     </Flex>
  //   )
  // },
  {
    align: 'right',
    title: `手续费`,
    dataIndex: 'filledFee',
    width: 180,
    render: (filledFee, row) => {
      let [baseSymbol, quoteSymbol] = row.market.split('-')
      return (
        <Flex alignItems="center" justifyContent="flex-end">
          <Text mr={'4px'}>{filledFee}{row.side === 'BUY' ? baseSymbol : quoteSymbol }</Text>
        </Flex>
      )
    }
  },
  {
    align: 'right',
    title: '状态',
    dataIndex: 'status',
    key: 'status',
    width: 100,
    render: (status, row) => (
      <Text>{ENUM_ORDER_STATUS.toText(status)}</Text>
    )
  },
];

class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      list: [],
      filters: {
        market: undefined,
        start: undefined,
        end: undefined,
        side: undefined,
        status: undefined
      }
    }
  }

  componentDidMount() {
  }

  fetchData = async function (pageParams) {
    let { dexAccount: { accountId, apiKey } } = this.props
    let { filters: { market, side, start, end, status } } = this.state
    let { offset, limit } = utils.formatPageParams(pageParams)

    let resp = await api.getOrders({
      accountId,
      apiKey,
      offset,
      limit,
      market,
      side,
      start,
      end,
      statuses: status ? [status] : undefined
    })
    let { totalNum, orders } = resp || {}

    orders = orders || []
    return {
      // TODO: 目前没有总数
      count: totalNum || 0,
      rows: orders
    }
  }.bind(this)
  
  onDataFetched = function (list) {
    console.info({list})
    this.setState({list})
  }.bind(this)

  renderFilters () {
    let { markets: marketsStore } = this.props
    let markets = marketsStore.markets.map(item => item.market)
    return (
      <Flex justifyContent="flex-end" flex={1}>
        <Select
          placeholder="全部交易对"
          allowClear
          style={{ width: 140, color: '#323232', marginRight: '20px' }}
          onChange={async (market) => {
            console.info({market})
            await this.setState(({ filters }) => {
              filters.market = market
              return { filters }
            })
            this.pagination.reset()
          }}
        >
          {
            markets.map(market => (
              <Option key={market} value={market}>{market}</Option>
            ))
          }
        </Select>
        <Select
          placeholder="全部类型"
          allowClear
          style={{ width: 140, color: '#323232', marginRight: '20px' }}
          onChange={async (side) => {
            console.info({side})
            await this.setState(({ filters }) => {
              filters.side = side
              return { filters }
            })
            this.pagination.reset()
          }}
        >
          <Option value={'BUY'}>{'买入'}</Option>
          <Option value={'SELL'}>{'卖出'}</Option>
        </Select>
        <Select
          placeholder="全部状态"
          allowClear
          style={{ width: 140, color: '#323232', marginRight: '20px' }}
          onChange={async (status) => {
            console.info({status})
            await this.setState(({ filters }) => {
              filters.status = status
              return { filters }
            })
            this.pagination.reset()
          }}
        >
          {
            _.map(ENUM_ORDER_STATUS.getOptions(), status => (
              <Option key={status.value} value={status.value}>{status.label}</Option>
            ))
          }
        </Select>
        <RangePicker placeholder={['开始日期', '结束日期']} onChange={async range => {
          let [start, end] = range
          start = start && start.startOf('day').toDate().valueOf()
          end = end && end.endOf('day').toDate().valueOf()

          console.info({start, end})
          await this.setState(({ filters }) => {
            filters.start = start
            filters.end = end
            return { filters }
          })
          this.pagination.reset()
        }} />
      </Flex>
    )
  }

  render() {
    let { list } = this.state

    return (
      <Box>
        { this.renderFilters() }
        <Table rowKey="tokenId" columns={columns} dataSource={list}
          className="mine-table"
          pagination={false}
          style={{ minHeight: '450px' }}
          locale={{emptyText: '暂无数据'}}
        />
        <Pagination ref={pagination => this.pagination = pagination} fetchData={this.fetchData} onFetched={this.onDataFetched}></Pagination>
      </Box>
    )
  }
}

const mapStateToProps = ({ dexAccount, markets }) => {
  return {
    dexAccount,
    markets
  };
};

const mapDispatchToProps = ({ order }) => {
  return {
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))