import React from 'react';
import { connect } from 'react-redux';
import numeral from 'numeral'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { TVChartContainer } from './TVChartContainer';
// import TVChartContainer from './TVChartContainer/index.tsx';
import { Dropdown, Icon, Menu } from 'antd'
import utils from 'utils'

const units = ['USD', 'CNY']

class TVChart extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      unit: 'USD'
    }
  }

  componentDidMount() {
  }

  renderUnits () {
    return (
      <Menu onClick={({key}) => {
        console.info(key)
      }}>
      {
        units.map((unit) => (
          <Menu.Item key={unit}>
            <Text fontSize={[0, 0, 0]} color='hint'>{ unit }</Text>
          </Menu.Item>
        ))
      }
      </Menu>
    )
  }

  render() {
    let { markets: { currentMarket }, trade: { latestPrice, variation }, candleStick: { dayInfo }, rate } = this.props

    if (!currentMarket) return <></>

    let { baseToken, quoteToken, market } = currentMarket
    return (
      <Box {...this.props}>
        <Flex height={50} px={12} py={'5px'} justifyContent="space-between">
          <Box pr={15}>
            <Text mt={'4px'} fontSize={16} lineHeight={'18px'} fontWeight={600}>
              {baseToken}/{quoteToken}
            </Text>
            <Flex color={variation >= 0 ? 'success' : 'failure'} mt={'0px'} alignItems="center" fontWeight={500}>
              <Text fontSize={14}>{latestPrice}</Text>
              <Text width="60px" ml={'10px'} fontSize={14}>{numeral(variation).format('+0.00%')}</Text>
            </Flex>
          </Box>
          <Box px={15}>
            <Dropdown overlay={this.renderUnits()} placement="bottomCenter" overlayClassName="nav-menu">
              <Flex mt={'3px'} alignItems="center">
                <Text color='#646478' mr={'5px'}>计价单位:</Text>
                <Text mr={'5px'}>{this.state.unit}</Text>
                <Icon type="caret-down" />
              </Flex>
            </Dropdown>
            <Text mt={'2px'} color='#FFFFFF'>{utils.asLegalMoney(rate, latestPrice, quoteToken, this.state.unit)}</Text>
          </Box>
          <Box px={15}>
            <Text color='#646478' mt={'3px'} mr={'5px'}>24H最高价</Text>
            <Text color='#FFFFFF' mt={'2px'}>{dayInfo.high}</Text>
          </Box>
          <Box px={15}>
            <Text color='#646478' mt={'3px'} mr={'5px'}>24H最低价</Text>
            <Text color='#FFFFFF' mt={'2px'}>{dayInfo.low}</Text>
          </Box>
          <Box px={15}>
            <Text color='#646478' mt={'3px'} mr={'5px'}>24H交易量</Text>
            <Text color='#FFFFFF' mt={'2px'}>{dayInfo.size}</Text>
          </Box>
          <Box flex={1}></Box>
        </Flex>
        <TVChartContainer style={{height: `calc(${this.props.height}px - 50px)`}} market={market} />
      </Box>
    )
  }
}

const mapStateToProps = ({ markets, trade, candleStick, rate }) => {
  return {
    markets,
    trade,
    candleStick,
    rate
  };
};

const mapDispatchToProps = dispatch => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TVChart);