import React, { forwardRef, useEffect, useState, useImperativeHandle, useRef } from 'react'
import { Modal, message } from 'antd'
import funcfy from './funcfyDialog'
import {
  Box,
  Flex,
  Text
} from 'rebass'

const Widget = ({
  style,
  onDestory,
  onSubmit = () => {}
}) => {
  const [visible, setVisible] = useState(false)
 
  useEffect(() => {
    show()
  }, [])

  const show = async () => {
    setVisible(true)
  }

  const cancel = async () => {
    setVisible(false)
    onDestory()
  }

  return (
    <Modal 
      bodyStyle={{backgroundColor: '#363636', color: '#d0d0d0'}}
      visible={visible}
      maskClosable={true}
      onOk={() => {
        cancel()
        onSubmit()
      }}
      onCancel={() => cancel()}
      footer={null}
      width={800}
    >
      <Box px={20}>
        <Text>您当前的浏览器不支持MetaMask插件，为了不影响您的交易，请使用支持MetaMask的浏览器。</Text>
        <Text>推荐使用：火狐浏览器、谷歌浏览器、360浏览器</Text>
        <Text>查看教程</Text>
      </Box>
    </Modal>
  )
}

export default (props) => funcfy(Widget, props)
