import _ from 'lodash'
import moment from 'moment'
import React from 'react';
import numeral from 'numeral'
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Table, Tooltip, Divider, Checkbox, Menu, Dropdown, Icon } from 'antd';
import { TabNav, CoinInput, BillPreCheck } from './Common'
import { TransferModal } from 'widgets/Dialog'
import { ENUM_ORDER_STATUS } from 'utils/enum'
import utils from 'utils';
import { notification } from 'antd';

const BigNumber = require('bignumber.js');

const tabs = [
  { text: '当前委托', value: 'current' },
  { text: '历史委托', value: 'history' },
]

class MyOrder extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
  }

  onTabChange (tab) {
    this.props.updateFilterCond({
      type: tab.value,
      page: 1,
      pageSize: 50
    })
    this.props.fetchOrders()
  }

  onChangeShowAll (e) {
    // 避免 onchange 事件进一步冒泡到 tab 的同名事件。
    e.stopPropagation()
    this.props.updateFilterCond({
      allMarket: e.target.checked,
      page: 1,
      pageSize: 50
    })
    this.props.fetchOrders()
  }

  render() {
    let { order, cancelAnOrders, cancelAllOrders } = this.props

    const filters = [
      { text: '所有状态', value: 'ALL' },
      { text: '已成交', value: 'PROCESSED' },
      { text: '已撤销', value: 'CANCELLED' },
    ]

    let columns = [
      {
        align: 'left',
        title: `时间`,
        dataIndex: 'createdAt',
        width: 120,
        render: (unused, row) => {
          return (<Text>{moment(row.createdAt).format('MM-DD HH:mm')}</Text>)
        }
      },
      {
        align: 'left',
        title: `交易对`,
        dataIndex: 'market',
        width: 100,
        render: (market, row) => {
          let [baseSymbol, quoteSymbol] = market.split('-')
          return (<Flex>{baseSymbol}<Text color="hint">/{quoteSymbol}</Text></Flex>)
        }
      },
      {
        align: 'center',
        title: '方向',
        dataIndex: 'side',
        width: 30,
        render: (unused, row) => {
          return (<Text color={row.side === 'BUY' ? "success" : "failure"}>{row.side === 'BUY' ? "买入" : "卖出"}</Text>)
        }
      },
      {
        align: 'right',
        title: `价格`,
        dataIndex: 'price',
        width: 120,
        render: price => (
          <Text>{price}</Text>
        )
      },
      {
        align: 'right',
        title: `数量`,
        dataIndex: 'size',
        width: 120,
        render: size => (
          <Text>{size}</Text>
        )
      },
      {
        align: 'right',
        title: `成交金额`,
        dataIndex: 'filledVolume',
        width: 120,
        render: filledVolume => (
          <Text>{filledVolume}</Text>
        )
      },
      {
        align: 'right',
        title: `已成交`,
        dataIndex: 'filledSize',
        width: 155,
        render: (filledSize, row) => (
          <Flex alignItems="center" justifyContent="flex-end">
            <Text mr={'4px'}>{filledSize}</Text>
            <Divider style={{position: 'unset', background: '#646478', margin: '0 2px'}} type="vertical" />
            <Text width={34} >{row.filled}</Text>
          </Flex>
        )
      },
    ];

    if (order.cond.type === 'current') {
      columns.push({
        align: 'right',
        title: `操作`,
        dataIndex: '',
        key: 'ops',
        width: 80,
        render: (unused, row) => (
          <Text color="blue" sx={{ cursor: 'pointer' }} onClick={async () => {
            await cancelAnOrders(row)
            notification['success']({
              duration: 5,
              message: '取消成功'
            })
          }}>撤销</Text>
        )
      })
    } else {
      columns.push({
        align: 'right',
        title: `状态`,
        dataIndex: 'status',
        key: 'ops',
        width: 80,
        render: (status, row) => (
          <Text>{ENUM_ORDER_STATUS.toText(status)}</Text>
        )
      })
    }

    return (
      <Flex {...this.props} flexDirection="column">
        <TabNav tabs={tabs} onChange={tab => this.onTabChange(tab)}>
          <Box flex={1}></Box>
          <Checkbox onChange={e => this.onChangeShowAll(e)} className="all-paire-type">显示所有币种</Checkbox>
          {
            utils.renderIf(order.cond.type === 'current')(
              <Text width={`100px`} px={10} textAlign="right" color="blue" onClick={() => cancelAllOrders()}
                sx={{ cursor: 'pointer' }}
              >
                撤销最新10条
              </Text>
            )
          }
          {
            utils.renderIf(order.cond.type === 'history')(
              <Dropdown overlay={
                  <Menu onClick={({key: index}) => {
                    let historyStatus = filters[index].value
                    this.props.updateFilterCond({
                      historyStatus,
                      page: 1,
                      pageSize: 50
                    })
                    this.props.fetchOrders()
                  }}>
                  {
                    filters.map((menu, index) => (
                      <Menu.Item key={index}>
                        <Text fontSize={[0, 0, 0]} color='hint'>{ menu.text }</Text>
                      </Menu.Item>
                    ))
                  }
                  </Menu>
                }
                placement="bottomCenter" overlayClassName="nav-menu"
              >
                <Flex width={`100px`} px={10} justifyContent="flex-end" height={40} lineHeight={'40px'} alignItems="center">
                  {_.find(filters, {value: order.cond.historyStatus}).text}<Icon type="caret-down" />
                </Flex>
              </Dropdown>
            )
          }
        </TabNav>
        <Flex bg="backgroundTint" px={15} flex={1}>
          <Table columns={columns} dataSource={order.list} align="right"
            style={{flex: 1}}
            tableLayout="auto"
            className="exchange-table"
            pagination={false}
            scroll={{ y: this.props.height - 70 }}
            // height={this.props.height - 50}
            locale={{emptyText: '暂无数据'}}
          />
        </Flex>
      </Flex>
    )
  }
}

const mapStateToProps = ({ order }) => {
  return {
    order,
  };
};

const mapDispatchToProps = ({ order }) => {
  return {
    updateFilterCond: order.updateFilterCond,
    fetchOrders: order.fetchOrders,
    cancelAnOrders: order.cancelAnOrders,
    cancelAllOrders: () => utils.withLoading(() => order.cancelAllOrders())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyOrder);