export function renderIf (predicate) {
  return elemOrThunk =>
    predicate ? (_.isFunction(elemOrThunk) ? elemOrThunk() : elemOrThunk) : null;
}

export function compareAppVersion(remoteVersion, localVersion){ 
  function toNum(a) {
    a = a.toString();
    //也可以这样写 var c=a.split(/\./);
    var c=a.split('.');
    var num_place=["","0","00","000","0000"],r=num_place.reverse();
    for (var i=0;i<c.length;i++){ 
        var len=c[i].length;       
        c[i]=r[len]+c[i];  
    } 
    var res= c.join(''); 
    return res; 
  } 

  let rvNum = toNum(remoteVersion)
  let lvNum = toNum(localVersion)
  if (rvNum > lvNum) {
    return 1
  } else if (rvNum === lvNum) {
    return 0
  } else {
    return -1
  }
}

// 被 promisefy 的异步函数，需满足以下2点要求
// 1. 最后一个参数是函数
// 2. 回调函数的参数为 (err, result)，前面是可能的错误，后面是正常的结果
function promisefy (asyncFunc) {
  function promiseFunc (...args) {
    // args 为实际调用时，传的前 n-1 个参数。
    return new Promise((resolve, reject) => {
      let preArgs = _.slice(args, 0, args.length)
      asyncFunc(...preArgs, function (err, result) {
        if (err) {
          reject(new Error(err))
        } else {
          resolve(result)
        }
      })
    })
  }
  return promiseFunc
}

// 回调函数的参数为 (result)
function promisefyNoError (asyncFunc) {
  function promiseFunc (...args) {
    // args 为实际调用时，传的前 n-1 个参数。
    return new Promise((resolve, reject) => {
      let preArgs = _.slice(args, 0, args.length)
      asyncFunc(...preArgs, function (result) {
        resolve(result)
      })
    })
  }
  return promiseFunc
}

export async function playVoice (audio, audioPath) {
  return new Promise((resolve, reject) => {
    audio.src = audioPath
    audio.play()
    console.info(audio.src, audio.duration)
    audio.onended = function() {
      resolve()
    }
  })
}

export function parseUlrParams (name) {
  let reg = new RegExp('(^|&|\\?|%3F)' + name + '(=|%3D)([^&]*)(&|$)')
  let r = window.location.search.substr(1).match(reg)
  return r && r[3]
}

export function waitFor (ms) {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve()
    }, ms)
  })
}

export function shadeSn(sn) {
  if (!sn) return
  let chars = sn.split('')
  chars = _.fill(chars, '*', 2, chars.length - 6)
  return chars.join('')
}

export function shadePhone(phone) {
  if (!phone) return
  let chars = phone.split('')
  chars = _.fill(chars, '*', 3, chars.length - 4)
  return chars.join('')
}

export function checkBrowser () {
  const browser = detect()
  // TODO 360
  return _.includes(['firefox', 'chrome', '360'], browser.name)
}

export function checkPassword (passwordHash, relayAccount) {
  let exchangeAccount = new Account(window.wallet);
  const keyPair = exchangeAccount.generateKeyPair(passwordHash);
  console.info(passwordHash, keyPair.publicKeyX, relayAccount.publicKeyX)
  return (
    keyPair.secretKey &&
    keyPair.publicKeyX === relayAccount.publicKeyX &&
    keyPair.publicKeyY === relayAccount.publicKeyY
  )
}

export async function genApiKey (accountId, passwordHash) {
  let exchangeAccount = new Account(window.wallet);
  const keyPair = exchangeAccount.generateKeyPair(passwordHash);
  let { publicKeyX, publicKeyY, secretKey } = keyPair

  const signed = exchangeAccount.getApiKey(
    accountId,
    publicKeyX,
    publicKeyY,
    secretKey
  );
  const data = {
    accountId: accountId,
    publicKeyX: publicKeyX,
    publicKeyY: publicKeyY
  };
  // 服务端生成
  let resp = await api.getApiKey(data, signed.signature);
  return resp.account
}

export function genPasswordHash (address, password) {
  return lightConeUtils.keccakHash("LOOPRING" + address.toLowerCase() + lightConeUtils.keccakHash(password))
}

export function formatPageParams ({ page, pageSize }) {
  return {
    offset: (page - 1) * pageSize,
    limit: pageSize
  }
}

export async function withLoading (handler) {
  dispatch.appState.update({ loading: true })
  try {
    let result = await handler()
    dispatch.appState.update({ loading: false })
    return result
  } catch (e) {
    console.info("!!!!!!withLoading error", e)
    dispatch.appState.update({ loading: false })
  }
}

export function setImmediateInterval (callback, interval) {
  callback()
  return setInterval(() => callback(), interval)
}

export function getPrecision (num) {
  let parts = num.toString().split('.')
  return parts[1] ? parts[1].length : 0
}

export function asLegalMoney (rateTable, value, from, to = 'USD') {
  let list = rateTable[to]
  let item = _.find(list, {symbol: from})
  return item && (value * item.price || 0).toFixed(2) || 0
}

export function formatOrders (orders) {
  return orders.map(order => {
    let updatedOrder = { ...order }
    const [baseTokenSymbol, quoteTokenSymbol] = updatedOrder.market.split('-')
    let market = config.getMarketBySymbol(baseTokenSymbol, quoteTokenSymbol)
    let { pricePrecision, amountPrecision, totalPrecision } = market

    let ethToken = config.getTokenBySymbol('ETH')
    const filledFee = config.fromWEI('ETH', Number(order['filledFee']), ethToken.precision);
    
    const price = Number(order['price']).toFixed(pricePrecision)
    const size = config.fromWEI(baseTokenSymbol, Number(order['size']), amountPrecision);
    const filledSize = config.fromWEI(baseTokenSymbol, Number(order['filledSize']), amountPrecision);
    const volume = config.fromWEI(quoteTokenSymbol, Number(order['volume']), totalPrecision);
    const filledVolume = config.fromWEI(quoteTokenSymbol, Number(order['filledVolume']), totalPrecision);
  
    // let filledInNumber = order['side'].toUpperCase() === 'SELL' ? filledSize / size : filledVolume / volume
    let filledInNumber = filledSize / size
    filledInNumber = Math.floor(filledInNumber * 100);
    const filled = `${filledInNumber}%`;
  
    updatedOrder['price'] = price;
    updatedOrder['filledFee'] = filledFee;
    updatedOrder['size'] = size;
    updatedOrder['filledSize'] = filledSize;
    updatedOrder['volume'] = volume;
    updatedOrder['filledVolume'] = filledVolume;
    updatedOrder['filled'] = filled;
    return updatedOrder
  })
}

