import React from 'react';
import { connect } from 'react-redux';
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import {
  Pair,
  Trade,
  TVChart,
  PlaceOrder,
  MyOrder,
  Depth,
  EggAward
} from 'widgets'
import { BillPreCheck } from 'widgets/Common'
import utils from 'utils'

class Home extends React.Component {
  constructor(props) {
    super(props);
    // this.billPreCheck = React.createRef();
    this.state = {
      remainHeight: 447,
      largeScreen: false
    }
  }

  onWindowResize = function () {
    const windowHeight = document.body.clientHeight
    const remainHeight = windowHeight - 305

    const windowWidth = document.body.clientWidth

    this.setState({
      remainHeight,
      largeScreen: windowWidth >= 1920
    })
  }.bind(this)

  componentDidMount() {
    window.addEventListener('resize', this.onWindowResize);
    this.onWindowResize()

    this.billPreCheck.checkBrowser()
  }
  
  componentWillUnmount() {
    window.removeEventListener('resize', this.onWindowResize);
  }

  render() {
    let { remainHeight } = this.state
    return (
      <Flex variant='exchange' width="100%">
        <Flex width={[320, 320, 320]} flexDirection='column'>
          <Pair height={this.state.largeScreen ? '100%' : '55%'} />
          {
            utils.renderIf(!this.state.largeScreen)(
              <>
                <Box as='hr' variant='divider.exchangeX' />
                <Trade height={'45%'} px={14} />
              </>
            )
          }
        </Flex>
        <Box as='hr' variant='divider.exchangeY' />
        <Flex flex={1} flexDirection="column">
          <TVChart height={_.max([remainHeight * 260 / (260 + 130), 285])} />
          <Box as='hr' variant='divider.exchangeX' />
          <PlaceOrder />
          <Box as='hr' variant='divider.exchangeX' />
          <MyOrder height={_.min([remainHeight * 130 / (260 + 130), remainHeight - 285])} />
        </Flex>
        <Box as='hr' variant='divider.exchangeY' />
        <Depth />
        {
          utils.renderIf(this.state.largeScreen)(
            <>
              <Box as='hr' variant='divider.exchangeY' />
              <Trade width={320} px={14} />
            </>
          )
        }
        <BillPreCheck ref={ref => this.billPreCheck = ref}></BillPreCheck>
        <EggAward />
      </Flex>
    )
  }
}

const mapStateToProps = state => {
  return {
  };
};

const mapDispatchToProps = dispatch => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home);