import moment from "moment"
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Modal, Input, Form, Button, notification, Checkbox } from 'antd'
import {
  Box,
  Flex,
  Text
} from 'rebass'
import funcfy from './funcfyDialog'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import utils from 'utils'
import { FormErrorInfo } from 'widgets/Common'
import { UpsertPasswordModal } from '../Dialog'
import api from 'api'

const errorInfos = [
  '大写字母',
  '小写字母',
  '数字',
  '至少8个字符',
]

class Widget extends Component {
  constructor(props) {
    super(props)

    this.state = {
      visible: false
    }
  }

  componentDidMount () {
    this.setState({ visible: true })
    this.checkInput()
  }

  async checkInput () {
    // 必须稍微等下，因为被接管后的表单虽然可以继续使用onChange事件，但是onChange触发时，表单的校验结果可能还没做完。
    await utils.waitFor(50)

    // 修改表单校验的规则：如果没有输入任何东西，默认全部错误。而表单校验本身的规则是，不输入，则无错误。
    let password = this.props.form.getFieldProps('password').value
    if (!password) {
      this.setState({errors: errorInfos})
      return 
    }

    let errors = this.props.form.getFieldsError()
    errors = _.chain(errors)
      .values()
      .flatten()
      .filter(error => error)
      .value()
    this.setState({errors})
  }

  async submit (e) {
    let { dexAccount } = this.props

    e.preventDefault();
    let validateFields = utils.promisefy(this.props.form.validateFields)

    let values
    try {
      values = await validateFields()
    } catch(err) {
      console.info(err)
      return
    }
    let { password, remember } = values

    const passwordHash = utils.genPasswordHash(dexAccount.address, password);
    const relayAccount = await api.lightconeGetAccount(dexAccount.address);
    console.info(password)
    let valid = utils.checkPassword(passwordHash, relayAccount)
    if (!valid) {
      notification['success']({
        duration: 5,
        message: (
          <div>
            <p>密码错误</p>
          </div>
        )
      });
      return
    }

    let accountId = relayAccount.accountId
    let apiKey = await utils.genApiKey(accountId, passwordHash)
    await this.props.updateAccount({
      accountId,
      passwordHash,
      apiKey,
      rememberMeStartTime: moment().format('YYYY-MM-DD HH:mm:ss'),
      rememberMeMinutes: (remember ? 24 : 1) * 60,
    })
    await this.props.startFetchBalance()
    await this.props.fetchOrders()

    if (relayAccount.isFreeze) {
      this.props.updateAccount({ state: ENUM_ACCOUNT_STATUS.LOCKED.getValue()})
      notification['success']({
        duration: 5,
        message: (
          <div>
            <p>已锁定.</p>
          </div>
        )
      });
    } else {
      this.props.updateAccount({ state: ENUM_ACCOUNT_STATUS.LOGGED_IN.getValue()})
      notification['success']({
        duration: 5,
        message: '登录成功'
      });
    }
    this.loginSunccess()
  }

  async loginSunccess () {
    this.setState({visible: false})
    this.props.onSucess()
    this.props.onDestory()
  }

  render () {
    let { state } = this.props.dexAccount
    let modalTitle = ENUM_ACCOUNT_STATUS.UNCREATED.equals(state) ? '设置交易密码' : '验证交易密码'

    const { getFieldDecorator, getFieldsValue } = this.props.form;
    let { remember } = getFieldsValue()
    
    const formItemLayout = {
      labelCol: { span: 4 },
      wrapperCol: { span: 20 },
    };
    const noLabelLayout = {
      wrapperCol: { span: 19, offset: 5 },
    };
    return (
      <Modal 
        // bodyStyle={{backgroundColor: '#363636', color: '#d0d0d0'}}
        title={modalTitle}
        visible={this.state.visible}
        maskClosable={true}
        onOk={() => {

        }}
        onCancel={() => {
          this.setState({visible: false})
          this.props.onFail()
          this.props.onDestory()
        }}
        footer={null}
        width={660}
        className="password-modal"
      >
        <Box pt={60} pb={68} justifyContent="center">
          <Box width={480} mx="auto">
            <Form className="upsert-password-form" onSubmit={(e) => {
              utils.withLoading(() => this.submit(e))
            }}>
              <Box>
                <Form.Item {...formItemLayout} label="密码" hasFeedback={false}>
                  {getFieldDecorator('password', {
                    rules: [
                      { required: true, message: '必填密码' },
                      { pattern: /[A-Z]+/, message: '大写字母' },
                      { pattern: /[a-z]+/, message: '小写字母' },
                      { pattern: /[0-9]+/, message: '数字' },
                      { min: 8, message: '至少8个字符' },
                    ],
                  })(
                    <Input.Password size="large" onChange={() => this.checkInput()} />
                  )}
                </Form.Item>
              </Box>
              <Box mt={26}>
                <Form.Item {...noLabelLayout}>
                  <FormErrorInfo errors={this.state.errors} items={errorInfos} />
                </Form.Item>
              </Box>
              <Form.Item {...noLabelLayout}>
                <Flex mt={20} justifyContent="space-between">
                  {getFieldDecorator('remember', {})(
                    <Checkbox style={{color: '#1890ff', fontSize: '14px'}}>记住密码</Checkbox>
                  )}
                  <Text fontSize={14} mx={15} color="#1890ff" onClick={() => {
                    this.props.onSucess()
                    this.props.onDestory()
                    UpsertPasswordModal()
                  }}
                    sx={{ cursor: 'pointer' }}
                  >
                    忘记密码
                  </Text>
                </Flex>
                { remember && (<Text lineHeight={'30px'} fontSize={14} color="#969696" ml={'6px'}>24 小时内交易无需输入密码</Text>) }
              </Form.Item>
              <Box mt={26}>
                <Form.Item {...noLabelLayout}>
                  <Button size="large" style={{width: '100%'}} type="primary" htmlType="submit" >确定</Button>
                </Form.Item>
              </Box>
            </Form>
          </Box>
        </Box>
      </Modal>
    )
  }
}

const WrappedForm = Form.create({ name: 'upsert_password_form' })(Widget);

const mapStateToProps = ({ metaMask, dexAccount }) => {
  return {
    metaMask,
    dexAccount
  };
};
const mapDispatchToProps = ({ dexAccount, balance, order }) => {
  return {
    updateAccount: dexAccount.updateAccount,
    startFetchBalance: balance.startFetchBalance,
    fetchOrders: order.fetchOrders,
  };
};
let connectedWidget = connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(WrappedForm)

export default function promisefy (props) {
  return new Promise((resolve, reject) => {
    funcfy(connectedWidget, {
      ...props,
      onSucess: resolve,
      onFail: reject,
    })
  })
}