import _ from 'lodash';
import React, { forwardRef, useEffect, useState, useImperativeHandle, Fragment } from 'react';
import { connect } from 'react-redux';
import { SignInModal, UpsertPasswordModal } from '../Dialog'
import PromisefyConfirm from 'components/PromisefyConfirm'
import {
  Box,
  Flex,
  Text
} from 'rebass'
import { Input, Button } from 'antd';
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
var detect = require('detect-browser').detect;

const Widget = forwardRef(({
  disabled,
  onSuccess = () => {},
  children,
  metaMask,
  dexAccount,
  signIn,
  initAccountFromMetaMask
}, ref) => {

  const checkBrowser = () => {
    const browser = detect()
    // TODO 360
    let valid = _.includes(['firefox', 'chrome', '360'], browser.name)
    if (valid) return true

    return PromisefyConfirm({
      bodyStyle:{backgroundColor: '#363636', color: '#d0d0d0'},
      renderContent: () => (
        <Box px={20}>
          <Text>您当前的浏览器不支持MetaMask插件，为了不影响您的交易，请使用支持MetaMask的浏览器。</Text>
          <Text>推荐使用：火狐浏览器、谷歌浏览器、360浏览器</Text>
          <Text>查看教程</Text>
        </Box>
      ),
      maskClosable: true,
      footer: null,
      width: 800
    })
  }

  const checkMetaMaskInstalled = () => {
    let valid = metaMask.installed
    if (valid) return true

    return PromisefyConfirm({
      // title: 'MetaMask',
      renderContent: () => (
        <Box px={20}>
          <Text>需要安装MetaMask，才能执行该操作。</Text>
          <Text>前往安装MetaMask</Text>
          <Text>查看教程</Text>
        </Box>
      ),
      maskClosable: true,
      footer: null,
      width: 800
    })
  }

  const checkNetwork = () => {
    let { isDesiredNetwork } = metaMask

    let invalidText = ''

    if (!isDesiredNetwork) {
      invalidText = '您的MetaMask当前登录的是以太坊测试网，请切换到以太坊主网络。'
    }

    if (!invalidText) return true

    return PromisefyConfirm({
      // title: 'MetaMask',
      renderContent: () => (
        <Box px={20}>
          <Text>{invalidText}</Text>
        </Box>
      ),
      maskClosable: true,
      footer: null,
      width: 800
    })
  }

  const checkAddress = () => {
    let { address } = dexAccount

    let invalidText = ''

    if (!address) {
      invalidText = '无法获取MetaMask地址，请重试'
    }

    if (!invalidText) return true

    return PromisefyConfirm({
      // title: 'MetaMask',
      renderContent: () => (
        <Box px={20}>
          <Text>{invalidText}</Text>
        </Box>
      ),
      maskClosable: true,
      footer: null,
      width: 800
    })
  }

  const loadMetamask = async () => {
    let { address, passwordHash } = dexAccount
    if (!address || !passwordHash) {
      await initAccountFromMetaMask()
    }
  }

  const checkSignedIn = async () => {
    let { address, passwordHash, state } = dexAccount
    let signined = address && passwordHash
    if (signined) return true

    if (state === ENUM_ACCOUNT_STATUS.UNCREATED.getValue()) {
      try {
        await UpsertPasswordModal({type: 'create'})
        return true
      } catch (e) {
        return false
      }
    } else if (state === ENUM_ACCOUNT_STATUS.UNSIGNED.getValue()) {
      try {
        await SignInModal()
        return true
      } catch (e) {
        return false
      }
    }
  }

  useImperativeHandle(ref, () => ({
    checkBrowser,
    checkSignedIn,
    check
  }))

  const check = async () => {
    // 是否装了 metamask
    await checkBrowser()
    // 是否登录 metamask
    await checkMetaMaskInstalled()
    // 检查网络
    await checkNetwork()
    // 确保账户已经初始化好了。如果缓存有，直接跳过；否则通过 MetaMask 获取地址，并初始化。（获取 MetaMask 地址，需要用户授权）
    await loadMetamask()
    // metamask 中是否已获取到正确的地址
    await checkAddress()
    // 地址是否已授权，且跟中继保存的公钥匹配
    await checkSignedIn()
    return true
  }

  return (
    <div onClick={async () => {
      if (disabled) return

      let isValid = await check()
      if (isValid) {
        onSuccess()
      }
    }}>
      {children}
    </div>
  )
})


const mapStateToProps = ({ metaMask, dexAccount }) => {
  return {
    metaMask,
    dexAccount
  };
};

const mapDispatchToProps = ({ dexAccount, metaMask }) => {
  return {
    initAccountFromMetaMask: metaMask.initAccountFromMetaMask,
    signIn: dexAccount.signIn
  };
};

export default connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(Widget)