import numeral from 'numeral';
import _ from 'lodash'
import moment from 'moment'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { Pagination } from 'components';
import { Table, Select, DatePicker } from 'antd';
import { ENUM_TX_STATUS } from 'utils/enum'
import { config } from 'librarys/lightcone_v3';
const { Option } = Select;
const { RangePicker } = DatePicker;

const transferColumns = [
  {
    title: '代币名称',
    dataIndex: 'symbol',
    key: 'symbol',
    width: 90,
    render: (symbol, record) => {
      return (
        <Flex>
          <Text>{symbol}</Text>
        </Flex>
      )
    },
  },
  {
    title: '时间',
    dataIndex: 'timestamp',
    key: 'timestamp',
    width: 160,
    render: (timestamp, row) => {
      return (<Text>{moment(timestamp).format('YYYY-MM-DD HH:mm:ss')}</Text>)
    }
  },
  {
    title: '划转自',
    dataIndex: 'txType',
    key: 'txType',
    width: 100,
    render: (txType, row) => {
      return (<Text>{txType === 'deposit' ? '我的钱包' : '币币账户'}</Text>)
    }
  },
  {
    title: '划转至',
    dataIndex: 'txType',
    key: 'txType2',
    width: 100,
    render: (txType, row) => {
      return (<Text>{txType === 'deposit' ? '币币账户' : '我的钱包'}</Text>)
    }
  },
  {
    title: '数量',
    dataIndex: 'realAmount',
    key: 'realAmount',
    width: 150,
    render: (realAmount, row) => (
      <Text>{numeral(realAmount).format('0,0.00000000')}</Text>
    )
  },
  {
    title: '手续费',
    dataIndex: 'feeAmount',
    key: 'feeAmount',
    width: 150,
    render: (feeAmount, row) => (
      <Text>{numeral(feeAmount).format('0,0.00000000')}ETH</Text>
    )
  },
  {
    title: '状态',
    dataIndex: 'status',
    key: 'status',
    width: 50,
    render: (status, row) => (
      <Text>{ENUM_TX_STATUS.toText(status)}</Text>
    )
  },
];

let tokens = config.getTokens()
const symbols = tokens.map(token => token.symbol)

class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filters: {
        symbol: undefined,
        side: undefined,
        status: undefined
      }
    }
  }

  async componentDidMount() {
    let { initialSymbol, initialSide } = this.props.history.location.data || {}
    if (initialSymbol) {
      await this.onSymbolChanged(initialSymbol)
    }
    if (initialSide) {
      await this.onSideChanged(initialSide)
    }
    this.props.fetchTransactions()
  }

  async onSymbolChanged (symbol) {
    this.setState(({ filters }) => {
      filters.symbol = symbol
      return { filters }
    })
    await this.props.updateFilterCond({ token: symbol })
  }

  async onSideChanged (side) {
    this.setState(({ filters }) => {
      filters.side = side
      return { filters }
    })

    let types = side && (side === 'WITHDRAW' ? ['onchainWithdrawal'] : ['deposit'])
    await this.props.updateFilterCond({ types })
  }

  async onStatusChanged (status) {
    this.setState(({ filters }) => {
      filters.status = status
      return { filters }
    })
    await this.props.updateFilterCond({ statuses: [status] })
  }

  renderFilters () {
    let { updateFilterCond, fetchTransactions } = this.props
    return (
      <Flex justifyContent="flex-end" flex={1}>
        <Select
          placeholder="全部币种"
          allowClear
          style={{ width: 140, color: '#323232' }}
          value={this.state.filters.symbol}
          onChange={symbol => {
            this.onSymbolChanged(symbol)
            fetchTransactions()
          }}
        >
          {
            symbols.map(symbol => (
              <Option key={symbol} value={symbol}>{symbol}</Option>
            ))
          }
        </Select>
        <Select
          placeholder="全部类型"
          allowClear
          style={{ width: 180, color: '#323232', marginLeft: '20px', marginRight: '20px' }}
          value={this.state.filters.side}
          onChange={async (side) => {
            this.onSideChanged(side)
            fetchTransactions()
          }}
        >
          <Option value={'DEPOSIT'}>{'我的钱包 转至 币币账户'}</Option>
          <Option value={'WITHDRAW'}>{'币币账户 转至 我的钱包'}</Option>
        </Select>
        <Select
          placeholder="全部状态"
          allowClear
          style={{ width: 140, color: '#323232', marginRight: '20px' }}
          onChange={async (status) => {
            this.onStatusChanged(status)
            fetchTransactions()
          }}
        >
          {
            _.map(ENUM_TX_STATUS.getOptions(), status => (
              <Option key={status.value} value={status.value}>{status.label}</Option>
            ))
          }
        </Select>
        <RangePicker placeholder={['开始日期', '结束日期']} onChange={async range => {
          let [start, end] = range
          start = start && start.startOf('day').toDate().valueOf()
          end = end && end.endOf('day').toDate().valueOf()

          console.info({start, end})
          await updateFilterCond({
            start,
            end
          })
          fetchTransactions()
        }} />
      </Flex>
    )
  }

  render() {
    let { transfer } = this.props
    // TODO: 此处 render 为啥老被刷新？
    
    let transfers = transfer.list.map(transfer => {
      let { symbol, amount, realAmount, feeAmount } = transfer
      let token = config.getTokenBySymbol(symbol)
      let ethToken = config.getTokenBySymbol('ETH')
      return {
        ...transfer,
        token,
        amount: config.fromWEI(token.symbol, amount, token.precision),
        realAmount: config.fromWEI(token.symbol, realAmount, token.precision),
        feeAmount: config.fromWEI(ethToken.symbol, feeAmount, ethToken.precision)
      }
    })

    return (
      <Box>
        { this.renderFilters() }
        <Table rowKey="tokenId" columns={transferColumns} dataSource={transfers}
          className="mine-table"
          pagination={false}
          style={{ minHeight: '450px' }}
          locale={{emptyText: '暂无数据'}}
        />
        {/* <Pagination ref={pagination => this.pagination = pagination} fetchData={this.fetchData} onFetched={this.onDataFetched}></Pagination> */}
      </Box>
    )
  }
}

const mapStateToProps = ({ transfer, markets }) => {
  return {
    transfer,
    markets
  };
};

const mapDispatchToProps = ({ transfer }) => {
  return {
    fetchTransactions: transfer.fetchTransactions,
    updateFilterCond: transfer.updateFilterCond,
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))