import _ from 'lodash'
import React, { forwardRef, useEffect, useState, useImperativeHandle, useRef } from 'react'
import { connect } from 'react-redux'
import { Modal, Form, Input, Select, Button, InputNumber, Slider, notification } from 'antd'
import funcfy from './funcfyDialog'
import {
  Box,
  Flex,
  Text,
  Image
} from 'rebass'
import switchSideIcon from 'icons/switchSide.png'
import utils, { renderIf } from 'utils'
import { config, common } from 'librarys/lightcone_v3';
import api from 'api'
const gasLimitInWEI = Number(config.getGasLimitByType("create").gasInWEI)

const { Option } = Select;

const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 20 },
};
const buttonItemLayout = {
  wrapperCol: { span: 20, offset: 4 },
};

const Widget = ({
  initialSymbol,
  initialType = 'deposit',
  onDestory,
  balance: balanceStore,
  walletBalance: walletBalanceStore,
  rate,
  fetchBalances,
  fetchWalletBalances,
  transfer
}) => {
  const [visible, setVisible] = useState(false)
  const [symbol, setSymbol] = useState(initialSymbol)
  const [type, setType] = useState(initialType)
  const [transferAmount, setTransferAmount] = useState(0)
  const [recommendedGasEth, setRecommendedGasEth] = useState(0)
  const [gasEth, setGasEth] = useState(0)

  useEffect(() => {
    // fetchBalances()
    fetchWalletBalances()
    show()

    api.getRecommendedGasPrice().then(resp => {
      let gasPriceInWEI = resp
      let gas = config.fromWEI('ETH', Number(gasPriceInWEI) * gasLimitInWEI, 6)
      gas = _.floor(gas, 6)

      setRecommendedGasEth(gas)
      setGasEth(gas)
    })
  }, [])

  const show = async () => {
    setVisible(true)
  }

  const cancel = async () => {
    setVisible(false)
    onDestory()
  }

  const getAvailable = () => {
    let available = 0
    if (symbol) {
      if (type === 'deposit') {
        let balance = _.find(walletBalanceStore.list, b => b.symbol === symbol)
        available = balance ? balance.amount : 0
      } else {
        let balance = _.find(balanceStore.list, b => b.token.symbol === symbol)
        available = balance ? balance.availAmount : 0
      }
      available = Number(available)
      available = _.floor(available, 6)
    }
    return available
  }

  const getWalletEthAvail = () => {
    let ethBalance = _.find(walletBalanceStore.list, b => b.symbol === 'ETH')
    return ethBalance ? ethBalance.amount : 0
  }

  let balances = balanceStore.list
  let balancesSymbols = balances.map(b => b.token.symbol)
  let walletBalances = walletBalanceStore.list
  let walletBalancesSymbols = walletBalances.map(b => b.symbol)
  let symbolOptions = _.union(balancesSymbols, walletBalancesSymbols)

  // 初始化 symbol 为symbolOptions的第一个元素
  console.info({symbol})
  if (!symbol) {
    if (symbolOptions[0]) {
      setSymbol(symbolOptions[0])
    }
    return <></>
  }

  // getFeeByType
  // 充值：用户手动选择矿工费。
  // 提现：从光锥的配置读，是固定的。
  // 充值且充ETH，可转账余额需要减去矿工费，但是最少是 0
  let walletEthAvail = getWalletEthAvail()
  let withdrawFee = config.fromWEI('ETH', config.getFeeByType('withdraw').feeInWEI, 6)
  
  // 可用金额
  let available = getAvailable()
  // 仅充值，且充值的是 ETH 时，才跟可充值总额联动。
  if (symbol === 'ETH' && type === 'deposit') {
    let ethAvailable = _.floor(available - gasEth, 6)
    available = _.max([ethAvailable, 0])
  }

  const submit = async (e) => {
    e.preventDefault()

    if (type === 'withdraw') {
      if (walletEthAvail < withdrawFee) {
        return
      }
    } else if (type === 'deposit') {
      if (gasEth >= walletEthAvail) {
        return
      }
    }
    if (transferAmount <= 0) return

    if (transferAmount > available) {
      notification.info({ message: '划转余额不足' })
      return
    }

    console.info({type, symbol, transferAmount, gasEth})
    let result = await transfer(type, symbol, transferAmount, gasEth)
    if (result) {
      cancel()
    }
  }

  return (
    <Modal
      title="资金划转"
      visible={visible}
      maskClosable={true}
      onCancel={() => cancel()}
      footer={null}
      width={660}
      className="password-modal"
    >
      <Box pt={60} pb={68} justifyContent="center">
        <Box width={480} mx="auto">
          <Form {...formItemLayout} onSubmit={submit}>
            <Form.Item label="币种">
              <Select style={{color: '#323232'}} defaultValue={symbolOptions[0]} value={symbol} onChange={value => {
                setSymbol(value)
                setTransferAmount(0)
              }} size="large">
                { symbolOptions.map(symbol => (<Option value={symbol}>{symbol}</Option>)) }
              </Select>
            </Form.Item>
            <Box mt={28}>
              <Form.Item label="从" size="large">
                <Input style={{color: '#323232'}} value={type === 'deposit' ? '我的钱包' : '币币账户'} size="large" />
              </Form.Item>
            </Box>
            <Image width={32} height={68} src={switchSideIcon}
              sx={{
                position: 'absolute',
                right: '55px',
                top: '205px',
                cursor: 'pointer'
              }} 
              onClick={() => {
                setType(type === 'deposit' ? 'withdraw' : 'deposit')
                setTransferAmount(0)
              }}
            />
            <Box mt={28}>
              <Form.Item label="转至" size="large">
                <Input style={{color: '#323232'}} value={type === 'deposit' ? '币币账户' : '我的钱包'} size="large" />
              </Form.Item>
            </Box>
            <Box mt={28}>
              <Form.Item label="划转数量">
                <InputNumber style={{color: '#323232', width: '100%'}} min={0} value={transferAmount} onChange={value => setTransferAmount(value)} size="large" />
              </Form.Item>
            </Box>
            <Box>
              <Form.Item {...buttonItemLayout}>
                <Flex>
                  <Text color="#969696" fontSize={14}>可划转数量 {available}</Text>
                  <Text ml={42} fontSize={14} color="#1890ff"
                    sx={{ cursor: 'pointer' }}
                    onClick={() => setTransferAmount(available)}
                  >
                    全部划转
                  </Text>
                </Flex>
              </Form.Item>
            </Box>
            {
              renderIf(available - transferAmount <= 0)(
                <Box mt={10}>
                  <Form.Item {...buttonItemLayout}>
                    <Text mt={15} color="#F0505A" lineHeight="14px">余额不够,无法完成划转!</Text>
                  </Form.Item>
                </Box>
              )
            }
            {
              renderIf(type === 'deposit')(
                <>
                  <Box mt={20}>
                    <Form.Item label='矿工费'>
                      <Input size="large" disabled value={`${gasEth} ETH ≈$${utils.asLegalMoney(rate, gasEth, 'ETH', 'USD')}`} />
                    </Form.Item>
                  </Box>
                  {renderIf(gasEth >= walletEthAvail)(
                    <Box mt={15}>
                      <Form.Item {...buttonItemLayout}>
                        <Text color="#F0505A" lineHeight="14px">{`矿工费不能超过钱包中的可用余额 ${walletEthAvail}ETH`}</Text>
                      </Form.Item>
                    </Box>
                  )}
                  <Box mt={28}>
                    <Form.Item {...buttonItemLayout}>
                      <Slider min={recommendedGasEth / 3} max={recommendedGasEth * 3} marks={{
                          [recommendedGasEth / 3]: '慢',
                          // [recommendedGasEth]: '中',
                          [recommendedGasEth * 3]: '快',
                        }}
                        step={0.000001} value={gasEth} onChange={value => setGasEth(value)}
                      />
                    </Form.Item>
                  </Box>
                </>
              )
            }
            {
              renderIf(type === 'withdraw')(
                <Box mt={28}>
                  <Form.Item label="手续费">
                    <Input style={{color: '#323232', width: '100%'}} disabled value={'0.002ETH'} size="large" />
                  </Form.Item>
                  {
                    renderIf(walletEthAvail < withdrawFee)(
                      <Form.Item {...buttonItemLayout}>
                        <Text mt={15} color="#F0505A" lineHeight="14px">{`手续费 ${withdrawFee}ETH， 钱包余额 ${walletEthAvail}ETH 不足`}</Text>
                      </Form.Item>
                    )
                  }
                  {
                    renderIf(walletEthAvail >= withdrawFee)(
                      <Form.Item {...buttonItemLayout}>
                        <Text mt={15} color="#F0505A" lineHeight="14px">提现手续费主要用于支付GAS费用，该费用将从您的钱包余额ETH中支付。</Text>
                      </Form.Item>
                    )
                  }
                </Box>
              )
            }
            <Box mt={20}>
              <Form.Item {...buttonItemLayout}>
                <Button size="large" style={{width: '100%'}} type="primary" htmlType="submit">确定</Button>
              </Form.Item>
            </Box>
          </Form>
        </Box>
      </Box>
    </Modal>
  )
}

const mapStateToProps = ({ balance, walletBalance, rate }) => {
  return {
    balance,
    walletBalance,
    rate
  };
};

const mapDispatchToProps = ({ balance, transfer, walletBalance }) => {
  return {
    fetchBalances: balance.fetchBalances,
    fetchWalletBalances: walletBalance.fetchAllBalances,
    transfer: (type, symbol, amount, gasEth) => {
      let gasPriceInWEI = config.toWEI('ETH', gasEth) / gasLimitInWEI
      let gasPrice = common.formatter.toGWEI(gasPriceInWEI)
      return utils.withLoading(() => transfer[type]({symbol, amount, gasPrice}))
    }
  };
};

let connectedWidget = connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(Widget)

export default (props) => funcfy(connectedWidget, props)
