import React from 'react';
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import { Table, Input, Icon } from 'antd';

class Pair extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: null
    }
  }

  componentDidMount() {
    this.setState({
      activeTab: this.props.tabs[0]
    })
  }

  async setActive (tab) {
    await this.props.onChange(tab)
    this.setState({
      activeTab: tab
    })
  }

  render() {
    let { tabs, ...restProps } = this.props
    return (
      <Flex {...restProps} height={40} alignItems='center' justifyContent='flex-start' >
        {
          tabs.map(tab => (
            <Flex width={80} height={40} alignItems='center' justifyContent='center'
              sx={{
                bg: tab === this.state.activeTab ? 'backgroundTint': 'background',
                cursor: 'pointer'
              }} onClick={() => this.setActive(tab)}>
              <Text fontSize={0} fontWeight={'bold'}>{tab.text}</Text>
            </Flex>
          ))
        }
        { this.props.children }
      </Flex>
    )
  }
}

const mapStateToProps = state => {
  return {
  };
};

const mapDispatchToProps = dispatch => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Pair);