import * as React from 'react';
import './index.less';
import { widget } from 'charting_library/charting_library.min';

import Datafeed from './api/';

function getLanguageFromURL() {
  const regex = new RegExp('[\\?&]lang=([^&#]*)');
  const results = regex.exec(window.location.search);
  return results === null
    ? null
    : decodeURIComponent(results[1].replace(/\+/g, ' '));
}

// Tutorial: https://medium.com/@jonchurch/tradingview-charting-library-js-api-setup-for-crypto-part-1-57e37f5b3d5a
export class TVChartContainer extends React.PureComponent {
  static defaultProps = {
    interval: '1',
    timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
    containerId: 'tv_chart_container',
    datafeedUrl: 'https://demo_feed.tradingview.com',
    libraryPath: '/charting_library/',
    chartsStorageUrl: 'https://saveload.tradingview.com',
    chartsStorageApiVersion: '1.1',
    clientId: 'tradingview.com',
    userId: 'public_user_id',
    fullscreen: false,
    autosize: true,
    studiesOverrides: {}
  };

  tvWidget = null;

  componentDidMount() {
    const widgetOptions = {
      debug: false,
      symbol: this.props.market,
      // BEWARE: no trailing slash is expected in feed URL
      datafeed: Datafeed,

      // https://github.com/tradingview/charting_library/wiki/Resolution
      interval: this.props.interval,

      timezone: this.props.timezone,
      container_id: this.props.containerId,
      library_path: this.props.libraryPath,
      frameborder: 0,
      toolbar_bg: '#0a0e1e',
      custom_css_url : "tradingview.css",
      overrides: {
        "mainSeriesProperties.style": 1,
        "symbolWatermarkProperties.color" : "#944",
        "volumePaneSize": "tiny",
        'paneProperties.background': "#0a0e1e",
      },

      // https://github.com/tradingview/charting_library/wiki/Localization
      locale: getLanguageFromURL() || 'en',
      
      // https://github.com/tradingview/charting_library/wiki/Featuresets
      disabled_features: [
        'use_localstorage_for_settings',
        // 'header_widget',
        // 'left_toolbar',
        'create_volume_indicator_by_default',
        'go_to_date',
        'source_selection_markers',
        'main_series_scale_menu',
        'source_selection_markers',
        'timeframes_toolbar',
        'header_symbol_search',
      ],
      enabled_features: ['move_logo_to_main_pane'],
      charts_storage_url: this.props.chartsStorageUrl,
      charts_storage_api_version: this.props.chartsStorageApiVersion,
      client_id: this.props.clientId,
      user_id: this.props.userId,
      fullscreen: this.props.fullscreen,
      autosize: this.props.autosize,
      studies_overrides: this.props.studiesOverrides,
      theme: 'Dark',

      time_frames: [
        /*
        {
          text: '1min',
          resolution: '1',
          description: '1 minute',
          title: '1min'
        },
        */
        // { text: '5min', resolution: '5', description: '5 Minutes', title: '5Min' },
        // { text: '30min', resolution: '30', description: '30 Minutes', title: '30Min' },
        // { text: '1hr', resolution: '60', description: '1 Hour', title: '1H' }
      ],
      symbols_types: ['LRC-ETH', 'LRC-USDT']
    };

    const tvWidget = new widget(widgetOptions);
    this.tvWidget = tvWidget;

    this.tvWidget.onChartReady(() => {
      this.chart = this.tvWidget.chart()

      // 创建均线
      this.chart.createStudy('Moving Average', false, false, [5, 'close', 0], null, {
        'Plot.color': '#34a9ff',
        'Plot.linewidth': 1
      })
      this.chart.createStudy('Moving Average', false, false, [10, 'close', 0], null, {
        'Plot.color': '#ffb620',
        'Plot.linewidth': 1
      })
      // this.chart.createStudy('Moving Average', false, false, [15, 'close', 0], null, {
      //   'Plot.color': '#8750ff',
      //   'Plot.linewidth': 1
      // })
      this.chart.createStudy('Moving Average', false, false, [30, 'close', 0], null, {
        'Plot.color': '#ff688f',
        'Plot.linewidth': 1
      })

      // 创建最新价水平线
      // this.chart.createShape({
      //     time: self.timestamp,
      //     channel:close
      // },{
      //     shape: 'horizontal_line'
      // })
    });
  }

  componentWillUnmount() {
    if (this.tvWidget !== null) {
      this.tvWidget.remove();
      this.tvWidget = null;
    }
  }

  componentWillReceiveProps(newProps) {
    if (
      this.props.market !== newProps.market
    ) {
      if (!this.chart || !newProps.market) return
      console.info(`#######更改 market：${newProps.market}`)

      // let market = newProps.market.replace('-', '/')
      this.tvWidget.chart().setSymbol(newProps.market, (...params) => {
        console.info("########  setSymbol callback", {params})
      })
      // changeSymbol(newProps.market)
    }
  }

  render() {
    return <div id={this.props.containerId} className={'TVChartContainer'} style={{height: this.props.style.height}} />;
  }
}
