import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { Divider, Table } from 'antd';
import { Account } from 'librarys/lightcone_v3'

class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
  }

  render() {
    let { dexAccount, metaMask, initAccountFromMetaMask } = this.props
    let { installed, startConnecting } = metaMask
    let { address, accountId, apiKey, passwordHash } = dexAccount

    if (startConnecting || !installed || !address || !passwordHash) return <></>

    // TODO: 优化。window.wallet可能没有初始化。初始化的时机是用户登录时(在 initAccountFromMetaMask 函数，也可考虑从此函数中分拆出来，无论如何让window.wallet初始化)，而没登录直接在缓存中加载，会有此问题。
    // if (!window.wallet) {
    //   window.wallet = ethereum.account.fromMetaMask(
    //     window.web3,
    //     address,
    //     address
    //   )
    // }
    let exchangeAccount = new Account(window.wallet)
    const keyPair = exchangeAccount.generateKeyPair(passwordHash)

    let infos = [
      { label: '账户ID', value: accountId },
      { label: '钱包地址', value: address },
      { label: 'API密钥', value: apiKey },
      { label: '公钥X', value: keyPair.publicKeyX },
      { label: '公钥Y', value: keyPair.publicKeyY },
      { label: '密钥', value: keyPair.secretKey },
    ]

    return (
      <Box {...this.props}>
        <Text mb={'14px'} color="#323232" fontSize={16} fontWeight="bold">API KEY</Text>
        <Divider/>
        <Box>
        {
          infos.map(info => (
            <Flex key={info.label} height={50}>
              <Text fontSize={14} width={100} color="#969696">{info.label}:</Text>
              <Text fontSize={14} color="#323232">{info.value}</Text>
            </Flex>
          ))
        }
        </Box>
      </Box>
    )
  }
}

const mapStateToProps = ({ dexAccount, metaMask }) => {
  return {
    dexAccount,
    metaMask
  };
};

const mapDispatchToProps = ({ metaMask }) => {
  return {
    initAccountFromMetaMask: metaMask.initAccountFromMetaMask
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))