import _ from 'lodash'
import api from 'api';
import { config } from 'librarys/lightcone_v3';

const initDeepthState = {
  level: 0,
  asks: [],
  bids: [],
  incrementalVersion: null
}

function scrollSellTableToBottom () {
  let tableDom = document.getElementsByClassName('sell-table')[0]
  if (!tableDom) return
  let tableBodyDom = tableDom.getElementsByClassName('ant-table-body')[0]
  if (!tableBodyDom) return
  tableBodyDom.scrollTop = tableBodyDom.scrollHeight
}

function formatFromWEI (resp, currentMarket, level) {
  let { asks, bids } = resp
  let { baseToken: baseSymbol, quoteToken: quoteSymbol, pricePrecision, amountPrecision, totalPrecision } = currentMarket

  asks = asks.map(ask => ({
    ...ask,
    type: 'ask',
    price: Number(ask.price).toFixed(pricePrecision - level),
    size: config.fromWEI(baseSymbol, ask.size, amountPrecision),
    volume: config.fromWEI(quoteSymbol, ask.volume, totalPrecision)
  }))
  bids = bids.map(bid => ({
    ...bid,
    type: 'bid',
    price: Number(bid.price).toFixed(pricePrecision - level),
    size: config.fromWEI(baseSymbol, bid.size, amountPrecision),
    volume: config.fromWEI(quoteSymbol, bid.volume, totalPrecision)
  }))

  return { asks, bids }
}

export default {
  state: initDeepthState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload })
  },
  effects: dispatch => ({
    async fetchDepth (payload, { markets, depth }) {
      if (!markets.currentMarket) return

      let { market: marketPair } = markets.currentMarket
      if (!marketPair) return

      const resp = await api.getDepth(marketPair, depth.level, 50)
      let { asks, bids } = formatFromWEI(resp, markets.currentMarket, depth.level)

      this.update({
        asks: _.orderBy(asks, ['price'], ['desc']),
        bids: _.orderBy(bids, ['price'], ['desc']),
        incrementalVersion: null
      })
      
      // 每次全量获取完 depth 后，重新订阅
      dispatch.socket.subscribe([ `depth&${marketPair}&${depth.level}` ])

      scrollSellTableToBottom()
    },
    async setLevel (level) {
      this.update({level})
      dispatch.depth.fetchDepth()
    },
    async scrollSellTable () {
      scrollSellTableToBottom()
    },
    async incrementalUpdate (payload, { depth: depthState, markets }) {
      let { market: marketPair } = markets.currentMarket
      if (!marketPair) return

      let { startVersion, endVersion, data } = payload
      let { incrementalVersion, asks, bids, level } = depthState

      if (incrementalVersion) {
        // 2510, 2510 ---> 2511, 2511
        let expectVersion = incrementalVersion + 1
        if (startVersion < expectVersion) {
          return
        }
        
        if (startVersion > expectVersion) {
          // 说明跳跃了，需要重新获取全量的。
          dispatch.depth.fetchDepth()
          return
        }
      }
      
      // 增量更新
      let newData = formatFromWEI(data, markets.currentMarket, level)
      asks = _.chain([...newData.asks, ...asks]).unionBy(a => a.price).reject(a => Number(a.size) === 0).orderBy(['price'], ['desc']).value()
      bids = _.chain([...newData.bids, ...bids]).unionBy(a => a.price).reject(a => Number(a.size) === 0).orderBy(['price'], ['desc']).value()

      this.update({
        asks,
        bids,
        incrementalVersion: endVersion
      })
    },
  })
}