import _ from 'lodash'
import React from 'react';
import numeral from 'numeral'
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { notification, Tooltip, Icon, Popover } from 'antd';
import { TabNav, CoinInput, BillPreCheck } from './Common'
import { TransferModal } from 'widgets/Dialog'
import utils from 'utils'
const BigNumber = require('bignumber.js');

const tabs = [
  { text: '限价', value: 'limitPrice' },
  // { text: '市价', value: 'marketPrice' },
]
const percentPositins = [ 0.25, 0.5, 0.75, 1 ]
class PlaceOrder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: tabs[0].value
    }
  }

  componentDidMount() {
  }

  renderLimitBuyArea (min, available) {
    let { markets, rate } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { baseToken, quoteToken, pricePrecision, amountPrecision, totalPrecision } = currentMarket
    let { placeOrder: { buyPrice, buyAmount, buyTotal }, updatePlaceOrder, placeBuyOrder } = this.props

    let error = buyTotal && buyTotal < min ? `交易额不能小于${min}${quoteToken}` : buyTotal > available ? `可用余额(${available} ${quoteToken})不足` : null
    return (
      <Flex flexDirection="column" flex={1} px={13} pb={10} pt={'3px'}>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={40}>单价</Text>
          <CoinInput
            precision={pricePrecision}
            value={buyPrice}
            approximate={`$ ${utils.asLegalMoney(rate, buyPrice, quoteToken, 'USD')}`}
            onChange={e => {
              let price = e.target.value
              let buyTotal = (new BigNumber(buyAmount || 0)).times(new BigNumber(price || 0))
              updatePlaceOrder({
                // 不能用 price，会导致输入框的 0 不能删除。
                buyPrice: price,
                buyTotal: buyTotal.toFixed(totalPrecision)
              })
            }}
            unit={quoteToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={40}>数量</Text>
          <CoinInput
            precision={amountPrecision}
            value={buyAmount}
            onChange={e => {
              let amount = e.target.value
              let total = (new BigNumber(amount || 0)).times(new BigNumber(buyPrice || 0))
              updatePlaceOrder({
                // 不能用 value，会导致输入框的 0 不能删除。
                buyAmount: amount,
                buyTotal: total.toFixed(totalPrecision),
              })
            }}
            unit={baseToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={40}>总额</Text>
          <CoinInput
            precision={totalPrecision}
            error={error}
            approximate={`$ ${utils.asLegalMoney(rate, buyTotal, quoteToken, 'USD')}`}
            unit={quoteToken}
            value={buyTotal}
            onChange={e => {
              let total = e.target.value
              let amount = buyPrice ? (new BigNumber(total || 0) / new BigNumber(buyPrice)) : 0
              updatePlaceOrder({
                buyAmount: total >= available ? _.floor(amount, amountPrecision) : _.ceil(amount, amountPrecision),
                // 不能用 total，会导致输入框的 0 不能删除。
                buyTotal: total
              })
            }}
          >
          </CoinInput>
        </Flex>
        <Flex variant="flexCenter" mt={9} ml={40} >
        {
          percentPositins.map(percent => (
            <Flex height={18} variant="flexCenter" lineHeight={'18px'} px={'6px'} py={'2px'}
              sx={{
                // background: '#141e32',
                // borderRadius: '2px',
                color:"hint",
                cursor: 'pointer'
              }}
              onClick={() => {
                let total = available * percent
                let amount = buyPrice ? (new BigNumber(total || 0) / new BigNumber(buyPrice)) : 0
                updatePlaceOrder({
                  buyAmount: total >= available ? _.floor(amount, amountPrecision) : _.ceil(amount, amountPrecision),
                  // 不能用 total，会导致输入框的 0 不能删除。
                  buyTotal: total >= available ? _.floor(total, totalPrecision) : _.ceil(total, totalPrecision),
                })
              }}
            >
            {numeral(percent).format('0%')}
            </Flex>
          ))
        }
        </Flex>
        <BillPreCheck disabled={buyTotal && !!error} onSuccess={() => {
          placeBuyOrder()
        }} depth={1}>
          {/* <Popover content={error} trigger={error ? "hover" : '' }> */}
            <Box mt={9} ml={40} height={34} bg="success" sx={{
                borderRadius: '3px',
                cursor: 'pointer'
              }}
              textAlign="center" lineHeight="34px" fontSize={2} fontWeight="bold">
              买入{baseToken}
            </Box>
          {/* </Popover> */}
        </BillPreCheck>
      </Flex>
    )
  }

  renderLimitSaleArea (min, available) {
    let { markets, rate } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { baseToken, quoteToken, pricePrecision, amountPrecision, totalPrecision } = currentMarket
    let { placeOrder: { sellPrice, sellAmount, sellTotal }, updatePlaceOrder, placeSellOrder } = this.props

    let error = sellAmount && Number(sellAmount) < min ? `交易数量不能小于${min}${baseToken}` : Number(sellAmount) > available ? `可用余额(${available} ${baseToken})不足` : null

    return (
      <Flex flexDirection="column" flex={1} px={13} pb={10} pt={'3px'}>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={40}>单价</Text>
          <CoinInput
            precision={pricePrecision}
            value={sellPrice}
            approximate={`$ ${utils.asLegalMoney(rate, sellPrice, quoteToken, 'USD')}`}
            onChange={e => {
              let price = e.target.value
              let sellTotal = (new BigNumber(sellAmount || 0)).times(new BigNumber(price || 0))
              updatePlaceOrder({
                // 不能用 price，会导致输入框的 0 不能删除。
                sellPrice: price,
                sellTotal: sellTotal.toFixed(totalPrecision)
              })
            }}
            unit={quoteToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={40}>数量</Text>
          <CoinInput
            precision={amountPrecision}
            error={error}
            value={sellAmount}
            onChange={e => {
              let amount = e.target.value
              let total = (new BigNumber(amount || 0)).times(new BigNumber(sellPrice || 0))
              updatePlaceOrder({
                // 不能用 amount，会导致输入框的 0 不能删除。
                sellAmount: amount,
                sellTotal: total.toFixed(totalPrecision),
              })
            }}
            unit={baseToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={40}>总额</Text>
          <CoinInput
            precision={totalPrecision}
            approximate={`$ ${utils.asLegalMoney(rate, sellTotal, quoteToken, 'USD')}`}
            unit={quoteToken}
            value={sellTotal}
            onChange={e => {
              let total = e.target.value
              let amount = sellPrice ? (new BigNumber(total || 0) / new BigNumber(sellPrice)) : 0
              updatePlaceOrder({
                sellAmount: total >= available ? _.floor(amount, amountPrecision) : _.ceil(amount, amountPrecision),
                // 不能用 total，会导致输入框的 0 不能删除。
                sellTotal: total
              })
            }}
          >
          </CoinInput>
        </Flex>
        <Flex variant="flexCenter" mt={9} ml={40} >
        {
          percentPositins.map(percent => (
            <Text height={18} lineHeight={'18px'}
              sx={{
                color:"hint",
                cursor: 'pointer'
              }}
              onClick={() => {
                let amount = available * percent
                let total = (new BigNumber(amount || 0)).times(new BigNumber(sellPrice || 0))
                updatePlaceOrder({
                  // 不能用 amount，会导致输入框的 0 不能删除。
                  sellAmount: _.floor(amount, amountPrecision),
                  sellTotal: total.toFixed(totalPrecision),
                })
              }}
            >
            {numeral(percent).format('0%')}
            </Text>
          ))
        }
        </Flex>
        <BillPreCheck disabled={sellAmount && !!error} onSuccess={() => placeSellOrder()} depth={1}>
          {/* <Popover content={error} trigger={error ? "hover" : '' }> */}
            <Box mt={9} ml={40} height={34} bg="failure" sx={{
                borderRadius: '3px',
                cursor: 'pointer'
              }}
              textAlign="center" lineHeight="34px" fontSize={2} fontWeight="bold">
              卖出{baseToken}
            </Box>
          {/* </Popover> */}
        </BillPreCheck>
      </Flex>
    )
  }

  renderMarketBuyArea (available) {
    let { markets, depth, currentPrecision } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { baseToken, quoteToken } = currentMarket
    let { placeOrder: { marketBuyTotal }, updatePlaceOrder, placeBuyOrder } = this.props

    return (
      <Flex flexDirection="column" flex={1} px={13} pb={10} pt={'3px'}>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={50}>买入价</Text>
          <CoinInput
            disabled={true}
            value={'以市价上最优价格买入'}
            unit={quoteToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={50}>交易额</Text>
          <CoinInput
            approximate={`$ 0.0`}
            value={marketBuyTotal}
            onChange={e => updatePlaceOrder({marketBuyTotal: e.target.value})}
            unit={quoteToken}
          >
          </CoinInput>
        </Flex>
        <Flex variant="flexCenter" mt={9} ml={40} >
        {
          percentPositins.map(percent => (
            <Flex height={18} variant="flexCenter" lineHeight={'18px'} px={'6px'} py={'2px'}
              sx={{
                // background: '#141e32',
                // borderRadius: '2px',
                color:"hint",
                cursor: 'pointer'
              }}
              onClick={() => {
                let marketBuyTotal = (available * percent).toFixed(6)
                updatePlaceOrder({ marketBuyTotal })
              }}
            >
            {numeral(percent).format('0%')}
            </Flex>
          ))
        }
        </Flex>
        <BillPreCheck onSuccess={() => placeBuyOrder()} depth={1}>
          <Box mt={9} ml={40} height={34} bg="success" sx={{
              borderRadius: '3px',
              cursor: 'pointer'
            }}
            textAlign="center" lineHeight="34px" fontSize={2} fontWeight="bold">
            买入{baseToken}
          </Box>
        </BillPreCheck>
      </Flex>
    )
  }

  renderMarketSaleArea (available) {
    let { markets, depth, currentPrecision } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    let { baseToken, quoteToken } = currentMarket
    let { placeOrder: { marketSellAmount }, updatePlaceOrder, placeSellOrder } = this.props

    return (
      <Flex flexDirection="column" flex={1} px={13} pb={10} pt={'3px'}>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={50}>卖出价</Text>
          <CoinInput
            disabled={true}
            value={'以市价上最优价格卖出'}
            onChange={e => updatePlaceOrder({sellPrice: e.target.value})}
            unit={quoteToken}
          >
          </CoinInput>
        </Flex>
        <Flex mt={'8px'} alignItems="center">
          <Text color="hint" width={50}>卖出量</Text>
          <CoinInput
            value={marketSellAmount}
            onChange={e => updatePlaceOrder({marketSellAmount: e.target.value})}
            unit={baseToken}
          >
          </CoinInput>
        </Flex>
        <Flex variant="flexCenter" mt={9} ml={40} >
        {
          percentPositins.map(percent => (
            <Text height={18} lineHeight={'18px'}
              sx={{
                color:"hint",
                cursor: 'pointer'
              }}
              onClick={() => {
                let marketSellAmount = (available * percent).toFixed(6)
                updatePlaceOrder({ marketSellAmount })
              }}
            >
            {numeral(percent).format('0%')}
            </Text>
          ))
        }
        </Flex>
        <BillPreCheck onSuccess={() => placeSellOrder()} depth={1}>
          <Box mt={9} ml={40} height={34} bg="failure" sx={{
              borderRadius: '3px',
              cursor: 'pointer'
            }}
            textAlign="center" lineHeight="34px" fontSize={2} fontWeight="bold">
            卖出{baseToken}
          </Box>
        </BillPreCheck>
      </Flex>
    )
  }

  getMinTradeValue (symbol) {
    let mapping = {
      'USDT': 2,
      'LRC': 100,
      'ETH': 0.01,
      'DKEY': 10,
    }
    return mapping[symbol] || 0
  }

  render() {
    let { markets: { currentMarket }, balance: { list: balances }, dexAccount: { passwordHash } } = this.props
    if (!currentMarket) return <></>

    let { baseToken, quoteToken } = currentMarket
    let baseBalance = _.find(balances, balance => balance.token.symbol === baseToken)
    let quoteBalance = _.find(balances, balance => balance.token.symbol === quoteToken)
    let availBase = baseBalance ? baseBalance.availAmount : 0
    let availQuote = quoteBalance ? quoteBalance.availAmount : 0
    return (
      <Box>
        <TabNav tabs={tabs} onChange={tab => this.setState({activeTab: tab.value})}>
          <Box flex={1}></Box>
          <Text color="hint">可用 {baseToken}: {availBase}</Text>
          <Text color="hint" ml={20}>可用 {quoteToken}: {availQuote}</Text>
          <Text mx={15} color="blue" onClick={() => {
            if (!passwordHash) {
              notification.info({ message: '请先登录' })
              return
            }
            TransferModal()
          }}
            sx={{ cursor: 'pointer' }}
          >
            划转资金
          </Text>
        </TabNav>
        {
          utils.renderIf(this.state.activeTab === 'limitPrice')(
            <Flex bg="backgroundTint" height={203}>
              {this.renderLimitBuyArea(this.getMinTradeValue(quoteToken), availQuote)}
              {this.renderLimitSaleArea(this.getMinTradeValue(baseToken), availBase)}
            </Flex>
          )
        }
        {
          utils.renderIf(this.state.activeTab === 'marketPrice')(
            <Flex bg="backgroundTint" height={203}>
              {this.renderMarketBuyArea(availQuote)}
              {this.renderMarketSaleArea(availBase)}
            </Flex>
          )
        }
      </Box>
    )
  }
}

const mapStateToProps = ({ placeOrder, markets, balance, dexAccount, rate }) => {
  return {
    placeOrder,
    markets,
    balance,
    dexAccount,
    rate
  };
};

const mapDispatchToProps = ({ placeOrder }) => {
  return {
    placeBuyOrder: placeOrder.placeBuyOrder,
    placeSellOrder: placeOrder.placeSellOrder,
    updatePlaceOrder: placeOrder.update
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PlaceOrder);