import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { ConnectedRouter } from 'connected-react-router';
import routes from './routes';
import { connect } from 'react-redux';
import { Spin, Icon } from 'antd';
import {
  Box,
} from 'rebass'
import './App.less';

const App = ({ history, ready, loading, connectToMetaMask, fetchMarkets, startSocket }) => {

  // 需等待 persist/REHYDRATE 完成
  if (!ready) return <div>加载中</div>

  return (
    <div>
      <Spin className="body-loading" spinning={loading} size="large" style={{height: '100vh', maxHeight: 'unset', zIndex:1001}}>
        <ConnectedRouter history={history}>{routes}</ConnectedRouter>
      </Spin>
    </div>
  )
};

App.propTypes = {
  history: PropTypes.object
};

const mapState = ({ appState }) => {
  return {
    ready: appState.ready,
    loading: appState.loading,
  }
}

const mapDispatch = ({ metaMask, markets, socket }) => {
  return {
    connectToMetaMask: metaMask.connectToMetaMask,
    fetchMarkets: markets.fetchMarkets,
    startSocket: socket.start
  };
};

export default connect(mapState, mapDispatch)(App)