import moment from 'moment'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import api from 'api'
import utils from 'utils'
import { notification } from 'antd';

const initState = {
  address: '',
  accountId: '',
  passwordHash: '',
  // 需要去服务端获取，因此不应该每次生成。
  apiKey: '',
  state: ENUM_ACCOUNT_STATUS.UNSIGNED.getValue(),
  rememberMeStartTime: null,
  rememberMeMinutes: null,
  canceledOrders: [],
}

export default {
  state: initState,
  reducers: {
    // 账户切换时，初始化当前账户，只保留地址。
    // TODO: 缓存不同地址对应的账户。否则 creating 状态，会被丢弃。
    switchAccount: (state, address) => {
      if (state.address === address) {
        return state
      } else {
        return {
          ...initState,
          address
        }
      }
    },
    clearAccountInfo ({ address }, payload) {
      return {
        ...initState,
        address
      }
    },
    updateAccount: (state, payload) => {
      return {
        ...state,
        ...payload
      }
    },
    checkAccountOvertime (state, payload) {
      let { rememberMeStartTime, rememberMeMinutes } = state
      if (rememberMeStartTime && rememberMeMinutes) {
        let validUntil = moment(rememberMeStartTime).add(rememberMeMinutes, 'minutes')
        if (moment().isAfter(validUntil)) {
          notification['success']({
            duration: 0,
            message: '授权超时',
            description: '请重新登录'
          });
          return {
            ...initState,
            address: state.address,
            accountId: state.accountId,
          }
        }
      }
      return state
    }
  },
  effects: dispatch => ({
    // 获取中继的 account 基本信息。
    async getAccount (address, rootState) {
      dispatch.balance.startFetchBalance()
      this.updateAccount({ address })

      const relayAccount = await api.lightconeGetAccount(address);
      if (!relayAccount) {
        this.updateAccount({ state: ENUM_ACCOUNT_STATUS.UNCREATED.getValue()})
        return
      } else if (relayAccount.isFreeze) {
        this.updateAccount({ state: ENUM_ACCOUNT_STATUS.LOCKED.getValue() })
      }
      // else {
      //   this.updateAccount({ state: ENUM_ACCOUNT_STATUS.UNSIGNED.getValue() })
      // }

      this.updateAccount({
        accountId: relayAccount.accountId,
      })

    },
    async loadAccount (address, { dexAccount }) {
      // 若缓存中有地址，需要处理。
      if (!dexAccount.address) return

      // 重要：现在启动时，不再检测钱包的地址是否跟缓存中的一致。引起启动时不能唤起 metamask（为了用户体验，不直接唤起），所以无法检查。
      // 存在问题：切换钱包后，再打开网页。若缓存的用户正确，可正常下单，但是实际上是另一个用户。
      // 优化点：下单前，无论如何都获取一次 metamask 的地址，并校验。

      // // 只有登录的地址中缓存中的不一致时，才重新初始化账户，否则继续使用缓存中的。
      // if (dexAccount.address !== address) {
      //   this.switchAccount(address)
      // }

      // 启动时，如果账户状态仍然是创建中，说明上次没创建完就关闭了。继续开始检查。
      if (dexAccount.state === ENUM_ACCOUNT_STATUS.CREATING.getValue()) {
        dispatch.dexAccount.checkAccountCreated()
      } else {
        // 重新获取account 基本信息，并刷新本地。
        // 重要: 必须放在 else 分支，因为中继没有 creating 态，会导致被更新为 uncreated 态。
        await dispatch.dexAccount.getAccount(dexAccount.address)
      }
    },
    async checkAccountCreated (payload, { dexAccount }) {
      let interval = setInterval(async () => {
        try {
          const relayAccount = await api.lightconeGetAccount(window.wallet.account)
          if (!relayAccount) return
          this.updateAccount({ accountId: relayAccount.accountId })

          let valid = utils.checkPassword(dexAccount.passwordHash, relayAccount)
          if (!valid) {
            this.updateAccount({
              state: ENUM_ACCOUNT_STATUS.CREATED.getValue()
            })
            // 不能 return，否则改密码，永远无法执行到下面的流程。
            // return
          }
          // 登录时，只验证密码，不更新account，直接在登录流程更新 apiKey。
          // 而 upsert 流程，需要在此处定时检查更新
          let apiKey = await utils.genApiKey(relayAccount.accountId, dexAccount.passwordHash)
          this.updateAccount({
            apiKey,
            state: ENUM_ACCOUNT_STATUS.LOGGED_IN.getValue()
          })
          notification['success']({
            duration: 5,
            message: '新密码已生效。'
          });

          clearInterval(interval)
        } catch (e) {}
      }, 5000);
    }
  })
}