import _ from 'lodash'
import { config } from 'librarys/lightcone_v3';
import utils from 'utils'
import api from 'api';

const initState = {
  list: [],
  page: 1,
  pageSize: 10
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
    appendBalance: (state, blance) => {
      let list = _.unionBy([ blance ], state.list, 'symbol')
      return {
        ...state,
        list: _.orderBy(list, 'symbol')
      }
    }
  },
  effects: dispatch => ({
    async fetchBalance (tokenSymbol, {dexAccount}) {
      let token = config.getTokenBySymbol(tokenSymbol)
      let amount = 0
      if (tokenSymbol.toUpperCase() === 'ETH') {
        amount = await api.getEthBalance(dexAccount.address)
      } else {
        amount = await api.getTokenBalance(dexAccount.address, tokenSymbol)
      }
      if (!amount) return

      amount = Number(
        config.fromWEI(
          tokenSymbol,
          amount,
          token.precision
        )
      );

      let blance = {
        symbol: token.symbol,
        name: token.name,
        amount: amount,
      }
      // 必须如此使用，因为 fetchAllBalances 中的多个 fetchBalance 会并发，此处拿到的rootState是旧的（await 之前的）。
      // 而 reducers 没有此问题，reducers是可重入的。
      this.appendBalance(blance)

      // let { list } = rootState.walletBalance
      // list = _.unionBy([ blance ], list, 'symbol')

      // this.update({
      //   list
      // })
    },
    async fetchAllBalances (payload, {  }) {
      // 我的资产页面，进来时批量拉取
      // 转入页面，每选一个币种，单独 fetchBalance 一次。 此页面的select下拉框用本地 store。
      let tokens = config.getTokens()
      tokens.forEach(token => {
        // 暂时去掉 WETH
        if (token.symbol === 'WETH') return
        dispatch.walletBalance.fetchBalance(token.symbol)
      })
    },
  })
}