import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import colors from 'colors';

import MyOrderTable from 'components/my-orders/MyOrderTable';
import MyDepositTable from 'components/my-orders/MyDepositTable';
import MyWithdrawalTable from 'components/my-orders/MyWithdrawalTable';

import './MyOrders.less';

import { Button, Icon } from 'antd';
const ButtonGroup = Button.Group;

class MyOrdersDetailView extends React.Component {
  state = {
    selectedKey: 'open'
  };

  render() {
    const unselectedButtonStyle = {
      borderTopLeftRadius: '8px',
      borderTopRightRadius: '8px',
      borderBottomLeftRadius: '0px',
      borderBottomRightRadius: '0px',
      height: '46px',
      width: '120px',
      backgroundColor: '#161616',
      color: colors.textMedContrast,
      borderWidth: '0px',
      marginRight: '6px',
      borderStyle: 'none',
      fontWeight: '14px',
      fontSize: '0.9rem'
    };

    const selectedButtonStyle = {
      borderTopLeftRadius: '8px',
      borderTopRightRadius: '8px',
      borderBottomLeftRadius: '0px',
      borderBottomRightRadius: '0px',
      height: '46px',
      width: '120px',
      backgroundColor: '#202020',
      color: colors.textMedContrast,
      borderWidth: '0px',
      marginRight: '6px',
      borderStyle: 'none',
      fontWeight: '14px',
      fontSize: '0.9rem'
    };

    const iconStyle = {
      fontSize: '14px',
      color: colors.textLowContrast,
      verticalAlign: 'middle'
    };

    var tableComponent;
    if (this.state.selectedKey === 'open') {
      tableComponent = <MyOrderTable />;
    } else if (this.state.selectedKey === 'deposit') {
      tableComponent = <MyDepositTable />;
    } else if (this.state.selectedKey === 'withdrawal') {
      tableComponent = <MyWithdrawalTable />;
    }

    return (
      <div
        style={{
          paddingTop: '30px',
          paddingLeft: '30px',
          paddingRight: '30px',
          paddingBottom: '30px'
        }}
      >
        <div
          style={{
            paddingLeft: '0px',
            paddingBottom: '0px',
            borderWidth: '0px'
          }}
        >
          <ButtonGroup
            style={{
              borderRadius: '0px',
              borderWidth: '0px',
              paddingBottom: '0px'
            }}
          >
            <Button
              className={
                this.state.selectedKey === 'open'
                  ? 'order-tab-button-active'
                  : 'order-tab-button'
              }
              style={
                this.state.selectedKey === 'open'
                  ? selectedButtonStyle
                  : unselectedButtonStyle
              }
              onClick={() => {
                this.setState({
                  selectedKey: 'open'
                });
              }}
            >
              Order
            </Button>
            <Button
              className={
                this.state.selectedKey === 'deposit'
                  ? 'order-tab-button-active'
                  : 'order-tab-button'
              }
              style={
                this.state.selectedKey === 'deposit'
                  ? selectedButtonStyle
                  : unselectedButtonStyle
              }
              onClick={() => {
                this.setState({
                  selectedKey: 'deposit'
                });
              }}
            >
              Deposit
            </Button>
            <Button
              className={
                this.state.selectedKey === 'withdrawal'
                  ? 'order-tab-button-active'
                  : 'order-tab-button'
              }
              style={
                this.state.selectedKey === 'withdrawal'
                  ? selectedButtonStyle
                  : unselectedButtonStyle
              }
              onClick={() => {
                this.setState({
                  selectedKey: 'withdrawal'
                });
              }}
            >
              Withdrawal
            </Button>
          </ButtonGroup>
        </div>
        {tableComponent}
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { layoutManager } = state;
  return { layoutManager };
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyOrdersDetailView);
