import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import appLayout from 'appLayout';

import { Table } from 'antd';
import Moment from 'moment';
import { config } from 'librarys/lightcone_v3';
const BigNumber = require('bignumber.js');

class MyOrderTable extends React.Component {
  render() {
    const tableTitleStyle = {
      color: '#666666',
      fontWeight: 400,
      fontSize: '14px',
      marginBottom: '0px'
    };

    const columns = [
      {
        title: '',
        dataIndex: 'padding',
        width: 2
      },
      {
        title: <div style={tableTitleStyle}>Side</div>,
        dataIndex: 'side',
        width: 100
      },
      {
        title: <div style={tableTitleStyle}>Market</div>,
        dataIndex: 'market',
        width: 120
      },
      {
        title: <div style={tableTitleStyle}>Size</div>,
        dataIndex: 'size',
        width: 120
      },
      {
        title: <div style={tableTitleStyle}>Price</div>,
        dataIndex: 'price',
        width: 120
      },
      {
        title: <div style={tableTitleStyle}>Fee</div>,
        dataIndex: 'fee',
        width: 200
      },
      {
        title: <div style={tableTitleStyle}>Date</div>,
        dataIndex: 'date',
        width: 300
      },
      {
        title: <div style={tableTitleStyle}>Status</div>,
        dataIndex: 'status',
        width: 100
      }
    ];

    const data = [];
    for (let i = 0; i < this.props.myOrders.myOrders.length; i++) {
      const order = this.props.myOrders.myOrders[i];
      const market = order.market;
      const tokens = market.split('-');
      const baseToken = tokens[0];

      console.log('OrderSize' + order.size);
      const size = config.fromWEI(baseToken, order.size, 8);

      var fee = config.fromWEI(baseToken, order.filledFee, 8);
      if (Number(fee) === 0) {
        fee = '-';
      }
      var status = '-';
      if (order.status === 'ORDER_STATUS_PROCESSING') {
        status = 'Open';
      } else if (order.status === 'ORDER_STATUS_PROCESSED') {
        status = 'Filled';
      } else if (order.status === 'ORDER_STATUS_FAILED') {
        status = 'Failed';
      } else if (
        order.status === 'ORDER_STATUS_CANCELLING' ||
        order.status === 'ORDER_STATUS_CANCELLED'
      ) {
        status = 'Cancelled';
      } else if (order.status === 'ORDER_STATUS_EXPIRED') {
        status = 'Expired';
      }
      data.push({
        key: i,
        side: (
          <div
            style={{
              height: '30px'
            }}
          >
            {order.side === 'BUY' ? 'Buy' : 'Sell'}
          </div>
        ),
        market: order.market,
        size: size,
        price: Number(order.price),
        fee: fee,
        date: Moment(order.timestamp).format('YYYY MM/DD HH:mm:ss'),
        status: status
      });
    }

    return (
      <div
        style={{
          paddingLeft: '0px',
          paddingTop: '0px',
          marginTop: '0px',
          height: appLayout.myOrderTableScreenHeight,
          borderWidth: '0px'
        }}
      >
        <Table
          style={{
            borderStyle: 'none',
            borderWidth: '0px'
          }}
          columns={columns}
          dataSource={data}
          pagination={{ pageSize: 10 }}
          scroll={{ y: appLayout.myOrderTableScreenHeight }}
        />
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { layoutManager, myOrders } = state;
  return { layoutManager, myOrders };
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyOrderTable);
