import React from 'react';
import { connect } from 'react-redux';

import colors from 'colors';
import appLayout from 'appLayout';
import { Line } from 'rc-progress';
import { Row, Table } from 'antd';
import BigNumber from 'bignumber.js';

class MyBalanceTable extends React.Component {
  render() {
    const tableTitleStyle = {
      color: '#666666',
      fontWeight: 400,
      fontSize: '14px',
      marginBottom: '0px'
    };

    const columns = [
      {
        title: '',
        dataIndex: 'padding',
        width: 2
      },
      {
        title: <div style={tableTitleStyle}>Asset</div>,
        dataIndex: 'asset',
        width: 90
      },
      {
        title: <div style={tableTitleStyle}>Name</div>,
        dataIndex: 'name',
        width: 120
      },
      /*
      {
        title: <div style={tableTitleStyle}>Holdings</div>,
        dataIndex: 'holdings',
        width: 140
      },
      */
      {
        title: <div style={tableTitleStyle}>Balance</div>,
        dataIndex: 'balance',
        width: 260
      },
      {
        title: <div style={tableTitleStyle}>Available Qty.</div>,
        dataIndex: 'quality',
        width: 260
      }
    ];

    // Find ETH and LRC
    var symbol = '';
    var name = '';
    var total = 0;
    var available = 0;
    var feeRate = 0.0;
    var percentage = 0;

    const data = [];
    const { balances } = this.props.balances;

    if (balances) {
      for (let i = 0; i < balances.length; i++) {
        const balance = balances[i];

        symbol = balance['tokenId'] === 0 ? 'ETH' : 'LRC';
        name = balance['tokenId'] === 0 ? 'Ethereum' : 'Loopring';
        const totalAmount = new BigNumber(balance['totalAmount']);
        total = totalAmount.dividedBy('1000000000000000000');
        const frozenAmount = new BigNumber(balance['frozenAmount']);
        percentage = 1 - frozenAmount.dividedBy(totalAmount).toNumber();
        percentage = Math.floor(percentage * 100);
        available = (total.toNumber() * percentage) / 100;
        feeRate = balance['tokenId'] === 0 ? 178.44 : 0.0346;

        data.push({
          key: i,
          asset: (
            <div
              style={{
                paddingRight: '40px',
                paddingTop: '4px',
                paddingBottom: '10px'
              }}
            >
              <Row>
                <div
                  style={{
                    color: colors.textMedContrast,
                    fontWeight: 400,
                    fontSize: '14px',
                    marginBottom: '0px'
                  }}
                >
                  {symbol}
                </div>
              </Row>
            </div>
          ),
          name: (
            <div
              style={{
                paddingRight: '40px',
                paddingTop: '4px',
                paddingBottom: '10px'
              }}
            >
              <Row>
                <div
                  style={{
                    color: colors.textMedContrast,
                    fontWeight: 400,
                    fontSize: '14px',
                    marginBottom: '0px'
                  }}
                >
                  {name}
                </div>
              </Row>
            </div>
          ),
          /*
          holdings: (
            <div
              style={{
                paddingRight: '80px',
                paddingTop: '4px',
                paddingBottom: '10px'
              }}
            >
              <Row>
                <div
                  style={{
                    color: colors.textMedContrast,
                    fontWeight: 400,
                    fontSize: '14px',
                    marginBottom: '0px'
                  }}
                >
                  {percentage}%
                </div>
              </Row>
              <Row
                style={{
                  height: '8px',
                  lineHeight: '8px'
                }}
              >
                <Line
                  strokeLinecap="square"
                  percent={percentage}
                  strokeWidth="4"
                  strokeColor={colors.theme}
                  trailWidth="4"
                  trailColor={colors.textLowContrast}
                />
              </Row>
            </div>
          ),
          */
          balance: (
            <div
              style={{
                paddingRight: '40px',
                paddingTop: '4px',
                paddingBottom: '10px'
              }}
            >
              <Row>
                <div
                  style={{
                    color: colors.textMedContrast,
                    fontWeight: 400,
                    fontSize: '14px',
                    marginBottom: '0px'
                  }}
                >
                  {total.toString(10)}
                </div>
              </Row>
            </div>
          ),
          quality: (
            <div
              style={{
                paddingRight: '40px',
                paddingTop: '4px',
                paddingBottom: '10px'
              }}
            >
              <Row>
                <div
                  style={{
                    color: colors.textMedContrast,
                    fontWeight: 400,
                    fontSize: '14px',
                    marginBottom: '0px'
                  }}
                >
                  {available}
                </div>
              </Row>
            </div>
          )
        });
      }
    }

    return (
      <div
        style={{
          paddingLeft: '0px',
          height: appLayout.myBalanceTableScreenHeight
        }}
      >
        <Table
          style={{
            borderStyle: 'none',
            borderWidth: '0px'
          }}
          columns={columns}
          dataSource={data}
          pagination={false}
          scroll={{ y: appLayout.myBalanceTableScreenHeight }}
        />
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { layoutManager, balances } = state;
  return { layoutManager, balances };
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyBalanceTable);
