import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import appLayout from 'appLayout';

import { Table } from 'antd';
import Moment from 'moment';
const BigNumber = require('bignumber.js');

class MyDepositTable extends React.Component {
  render() {
    const tableTitleStyle = {
      color: '#666666',
      fontWeight: 400,
      fontSize: '14px',
      marginBottom: '0px'
    };

    const columns = [
      {
        title: '',
        dataIndex: 'padding',
        width: 2
      },
      {
        title: <div style={tableTitleStyle}>Asset</div>,
        dataIndex: 'symbol',
        width: 120
      },
      {
        title: <div style={tableTitleStyle}>Amount</div>,
        dataIndex: 'amount',
        width: 120
      },
      {
        title: <div style={tableTitleStyle}>Fee</div>,
        dataIndex: 'fee',
        width: 200
      },
      {
        title: <div style={tableTitleStyle}>Date</div>,
        dataIndex: 'date',
        width: 300
      },
      {
        title: <div style={tableTitleStyle}>Status</div>,
        dataIndex: 'status',
        width: 100
      }
    ];

    const data = [];
    for (let i = 0; i < this.props.myOrders.transactions.length; i++) {
      const transaction = this.props.myOrders.transactions[i];
      if (transaction.txType !== 'DEPOSIT') {
        continue;
      }

      const amount = new BigNumber(transaction.amount)
        .dividedBy('1000000000000000000')
        .toString();
      var fee = new BigNumber(transaction.feeAmount)
        .dividedBy('1000000000000000000')
        .toString();
      if (fee === '0') {
        fee = '-';
      }
      var status = '-';
      if (transaction.status === 'TX_STATUS_PROCESSING') {
        status = 'Processing';
      } else if (transaction.status === 'TX_STATUS_PROCESSED') {
        status = 'Processed';
      }
      data.push({
        key: i,
        symbol: transaction.symbol,
        amount: amount,
        fee: fee,
        date: Moment(transaction.timestamp).format('YYYY MM/DD HH:mm:ss'),
        status: status
      });
    }

    return (
      <div
        style={{
          paddingLeft: '0px',
          paddingTop: '0px',
          marginTop: '0px',
          height: appLayout.myOrderTableScreenHeight,
          borderWidth: '0px'
        }}
      >
        <Table
          style={{
            borderStyle: 'none',
            borderWidth: '0px'
          }}
          columns={columns}
          dataSource={data}
          pagination={{ pageSize: 10 }}
          scroll={{ y: appLayout.myOrderTableScreenHeight }}
        />
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { layoutManager, myOrders } = state;
  return { layoutManager, myOrders };
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyDepositTable);
