function withoutThrow(func) {
  return async (...params) => {
    try {
      return await func(...params)
    } catch (error) {
      // debugger
      notification['error']({
        style: {
          backgroundColor: 'white'
        },
        message: String(error),
        duration: 10
      })
      return null
    }
  }
}

export function _parseResponse(response) {
  const data = response['data'];
  const resultInfo = data['resultInfo'];
  const errorData = data['error'];

  // Some API endpoints in the old versions don't have resultInfo.
  if (resultInfo) {
    const code = resultInfo['code'];
    if (code !== 0) {
      const errorMessage = resultInfo['message'];
      throw Error(errorMessage);
    }
    return data;
  } else if (errorData) {
    const code = errorData['code'];
    if (code !== 0) {
      const errorMessage = `${errorData['code']} ${errorData['message']}`;
      throw Error(errorMessage);
    }
    return data;
  } else {
    return data;
  }
}

export function parseResponse(response) {
  try {
    return _parseResponse(response)
  } catch (err) {
    // debugger
    notification['error']({
      style: {
        backgroundColor: 'white'
      },
      // message: 'Your deposit request failed. Please try again!',
      description: String(err),
      duration: 10
    });
  }
}

let relayApi = axios.create({
  // timeout: 10000,
  baseURL: serverUrl,
})

async function getTokenBalance(owner, symbol) {
  let token = config.getTokenBySymbol(symbol);
  let tokenAddress = token.address;
  const data = {
    owner,
    token: tokenAddress
  };

  try {
    const response = await relayApi.post('/tokenBalances', data);
    const responseData = response['data'];
    if (responseData['error'] === undefined) {
      return responseData['balances'];
    } else {
      throw new Error(responseData['error']['message']);
    }
  } catch (e) {
    return false;
  }
}

