import _ from 'lodash'
import moment from "moment"
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Modal, Input, Form, Button, notification, Checkbox, Slider } from 'antd'
import {
  Box,
  Flex,
  Text
} from 'rebass'
import { Account, config, common, Utils } from 'librarys/lightcone_v3';
import funcfy from './funcfyDialog'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import utils, { renderIf } from 'utils'
import api from 'api'
import { FormErrorInfo } from 'widgets/Common'

const errorInfos = [
  '大写字母',
  '小写字母',
  '数字',
  '至少8个字符',
  '输入的密码一致'
]
const gasLimitInWEI = Number(config.getGasLimitByType("create").gasInWEI)
const createAccountFee = Number(config.fromWEI('ETH', config.getFeeByType('create').feeInWEI, 6))

class Widget extends Component {
  constructor(props) {
    super(props)

    this.state = {
      errors: [],
      visible: false,
      recommendedGasEth: 0,
    }
  }

  componentDidMount () {
    this.setState({ visible: true })
    this.checkInput()
    this.props.fetchWalletBalances()

    api.getRecommendedGasPrice().then(resp => {
      // let gasPriceInWEI = common.formatter.fromGWEI(resp)
      let gasPriceInWEI = resp
      let gasEth = config.fromWEI('ETH', Number(gasPriceInWEI) * gasLimitInWEI, 6)

      this.setState({recommendedGasEth: _.floor(gasEth, 6)})
    })
  }

  async checkInput () {
    // 必须稍微等下，因为被接管后的表单虽然可以继续使用onChange事件，但是onChange触发时，表单的校验结果可能还没做完。
    await utils.waitFor(50)

    // 修改表单校验的规则：如果没有输入任何东西，默认全部错误。而表单校验本身的规则是，不输入，则无错误。
    let password = this.props.form.getFieldProps('password').value
    if (!password) {
      this.setState({errors: errorInfos})
      return 
    }
    
    let errors = this.props.form.getFieldsError()
    console.info(errors)
    errors = _.chain(errors)
    .values()
    .flatten()
    .filter(error => error)
    .value()
    
    let confirmPassword = this.props.form.getFieldProps('confirmPassword').value
    if (password !== confirmPassword) errors.push('输入的密码一致')
    this.setState({errors})
  }

  async submit (e) {
    e.preventDefault();
    let validateFields = utils.promisefy(this.props.form.validateFields)
    let values
    try {
      values = await validateFields()
    } catch(err) {
      return
    }
    let { password, remember, gasEth } = values
    let ethAvail = this.getEthAvail()
    console.info({gasEth, ethAvail})
    if (gasEth >= ethAvail) return
  
    let { type, dexAccount, updateAccount, checkAccountCreated } = this.props
    const passwordHash = utils.genPasswordHash(dexAccount.address, password);
    const nonce = await api.getNonce(window.wallet.account);
    const exchangeAccount = new Account(window.wallet);

    let gasPriceInWEI = config.toWEI('ETH', gasEth) / gasLimitInWEI
    const resp = await exchangeAccount.createOrUpdateAccount(
      passwordHash,
      nonce,
      common.formatter.toGWEI(gasPriceInWEI)
    );
    
    const signedEthereumTx = resp['signedTx'];
    const keyPair = resp['keyPair'];
    
    if (keyPair.secretKey === undefined) {
      throw new Error(
        'Failed to parse the response of account creation.'
      );
    }
    
    console.log('signedEthereumTx: ', signedEthereumTx);
    const response = await api.sendDeposit(signedEthereumTx);
    const data = response['result'];

    // TODO: Remove response
    console.log('Response of createOrUpdateAccount: ', data);

    notification['success']({
      duration: 5,
      message: type === 'modify' ? '新密码提交成功，生效需40分钟左右。新密码生效前，可以正常下单。' : '交易密码提交成功，生效需15秒左右。'
    });

    updateAccount({
      // TODO: 直接换浏览器，重置密码。那accountId如何存入 store?
      // accountId: relayAccount.accountId,
      state: ENUM_ACCOUNT_STATUS.CREATING.getValue(),
      passwordHash,
      rememberMeStartTime: moment().format('YYYY-MM-DD HH:mm:ss'),
      rememberMeMinutes: (remember ? 24 : 1) * 60,
    })
    checkAccountCreated()

    this.loginSunccess()
  }

  async loginSunccess () {
    this.setState({visible: false})
    this.props.onSucess()
    this.props.onDestory()
  }

  getEthAvail () {
    let { type = 'modify', walletBalance } = this.props
    let ethBalance = _.find(walletBalance.list, b => b.symbol === 'ETH')
    let ethAmount = ethBalance ? ethBalance.amount : 0
    if (type === 'create') {
      return _.max(ethAmount - createAccountFee, 0)
    } else {
      return ethAmount
    }
  }

  render () {
    let { type = 'modify', rate } = this.props
    const { getFieldDecorator, getFieldsValue } = this.props.form;
    let { remember, gasEth } = getFieldsValue()
    let { recommendedGasEth } = this.state
    let gasEthForShow = gasEth || recommendedGasEth
    let ethAvail = this.getEthAvail()

    const formItemLayout = {
      labelCol: { span: 5 },
      wrapperCol: { span: 19 },
    };
    const noLabelLayout = {
      wrapperCol: { span: 19, offset: 5 },
    };

    return (
      <Modal 
        // bodyStyle={{backgroundColor: '#363636', color: '#d0d0d0'}}
        title={`${type === 'create' ? '设置' : '修改'}交易密码`}
        visible={this.state.visible}
        maskClosable={true}
        onOk={() => {

        }}
        onCancel={() => {
          this.setState({visible: false})
          this.props.onFail()
          this.props.onDestory()
        }}
        footer={null}
        width={660}
        className="password-modal"
      >
        <Box pt={60} pb={68} justifyContent="center">
          <Box width={480} mx="auto">
            <Form className="upsert-password-form" onSubmit={(e) => {
              utils.withLoading(() => this.submit(e))
            }}>
              <Box>
                <Form.Item {...formItemLayout} label={`${type === 'create' ? '密码' : '新密码'}`} hasFeedback={false}>
                  {getFieldDecorator('password', {
                    rules: [
                      { required: true, message: '必填密码' },
                      { pattern: /[A-Z]+/, message: '大写字母' },
                      { pattern: /[a-z]+/, message: '小写字母' },
                      { pattern: /[0-9]+/, message: '数字' },
                      { min: 8, message: '至少8个字符' },
                    ],
                  })(
                    <Input.Password size="large" onChange={() => this.checkInput()} />
                  )}
                </Form.Item>
              </Box>
              <Box mt={20}>
                <Form.Item {...formItemLayout} label={`${type === 'create' ? '确认密码' : '确认新密码'}`}>
                  {getFieldDecorator('confirmPassword', {
                    rules: [
                      { required: true, message: '必填密码' },
                      {
                        validator: (rule, value, callback) => {
                          const { form } = this.props;
                          if (value && value !== form.getFieldValue('password')) {
                            callback('输入的密码一致');
                          } else {
                            callback();
                          }
                        }
                      }
                    ],
                  })(
                    <Input.Password size="large" onChange={() => this.checkInput()} />
                  )}
                </Form.Item>
              </Box>
              <Box mt={20}>
                <Form.Item {...noLabelLayout}>
                  <FormErrorInfo errors={this.state.errors} items={errorInfos} />
                </Form.Item>
              </Box>
              <Box mt={20}>
                <Form.Item {...formItemLayout} label='注册费'>
                  <Input size="large" disabled value={`0.005 ETH ≈ $${utils.asLegalMoney(rate, 0.005, 'ETH', 'USD')}`} />
                </Form.Item>
              </Box>
              <Box mt={20}>
                <Form.Item {...formItemLayout} label='矿工费'>
                  <Input size="large" disabled value={`${gasEthForShow} ETH ≈ $${utils.asLegalMoney(rate, gasEthForShow, 'ETH', 'USD')}`} />
                </Form.Item>
              </Box>
              {renderIf(gasEth >= ethAvail)(
                <Box mt={15}>
                  <Form.Item {...noLabelLayout}>
                    <Text color="#F0505A" lineHeight="14px">{`矿工费不能超过钱包中的可用余额 ${ethAvail}ETH`}</Text>
                  </Form.Item>
                </Box>
              )}
              <Form.Item {...noLabelLayout}>
                {getFieldDecorator('gasEth', {
                  initialValue: recommendedGasEth
                })(
                  <Slider min={recommendedGasEth / 3} max={recommendedGasEth * 3} marks={{
                    [recommendedGasEth / 3]: '慢',
                    // [recommendedGasEth]: '中',
                    [recommendedGasEth * 3]: '快',
                  }} step={0.000001} />
                )}
              </Form.Item>
              <Form.Item {...noLabelLayout}>
                <Flex mt={10} justifyContent="space-between">
                  {getFieldDecorator('remember', {
                    initialValue: false
                  })(
                    <Checkbox style={{color: '#1890ff', fontSize: '14px'}}>记住密码</Checkbox>
                  )}
                </Flex>
                { remember && (<Text lineHeight={'30px'} fontSize={14} color="#969696" ml={'6px'}>24 小时内交易无需输入密码</Text>) }
              </Form.Item>
              <Box mt={20}>
                <Form.Item {...noLabelLayout}>
                  <Button size="large" style={{width: '100%'}} type="primary" htmlType="submit" >确定</Button>
                </Form.Item>
              </Box>
            </Form>
          </Box>
        </Box>
      </Modal>
    )
  }
}

const WrappedForm = Form.create({ name: 'upsert_password_form' })(Widget);

const mapStateToProps = ({ metaMask, dexAccount, rate, walletBalance }) => {
  return {
    metaMask,
    dexAccount,
    rate,
    walletBalance
  };
};
const mapDispatchToProps = ({ dexAccount, walletBalance }) => {
  return {
    updateAccount: dexAccount.updateAccount,
    checkAccountCreated: dexAccount.checkAccountCreated,
    fetchWalletBalances: walletBalance.fetchAllBalances,
  };
};
let connectedWidget = connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(WrappedForm)

export default function promisefy (props) {
  console.info({props})
  return new Promise((resolve, reject) => {
    funcfy(connectedWidget, {
      ...props,
      onSucess: resolve,
      onFail: reject,
    })
  })
}