import _ from 'lodash'
import React, { Component } from 'react'
import PropTypes from 'prop-types';
import { Pagination } from 'antd';
import styles from './index.less';

class QhPagination extends Component {
  constructor (props) {
    super(props)
    
    this.state = {
      pageSize: props.initPageSize,
      page: 1,
      total: 0
    }
  }

  async componentDidMount () {
    if (this.props.autoload) {
      this.fetchDataByPage()
    }
  }

  componentWillUnmount() {
  }
  
  async reset () {
    await this.setState({
      page: 1,
      total: 0
    })
    return this.fetchDataByPage()
  }

  fetchDataByPage () {
    let pageParams = {
      page: this.state.page,
      pageSize: this.state.pageSize
    }
    return this.props.fetchData(pageParams).then(body => {
      this.setState({
        total: body.count
      })
      this.props.onFetched(body.rows)
    }).catch(error => { console.info(error.message) })
  }

  handleSizeChange = async function (size) {
    await this.setState({pageSize: size})
    this.fetchDataByPage()
  }.bind(this)

  handleCurrentChange = async function (currentPage) {
    await this.setState({page: currentPage})
    this.fetchDataByPage()
  }.bind(this)

  render () {
    return (
      <Pagination
        className="qhPagination"
        current={this.state.page}
        pageSize={this.state.pageSize}
        total={this.state.total}
        onChange={this.handleCurrentChange}
        onShowSizeChange={this.handleSizeChange}
        pageSizeOptions={['30', '50', '100', '200']}
        showQuickJumper
      />
    )
  }
}

QhPagination.defaultProps = {
  initPageSize: 30,
  autoload: true
};
  
QhPagination.propTypes = {
  initPageSize: PropTypes.number,
  fetchData: PropTypes.func,
  onFetched: PropTypes.func,
  autoload: PropTypes.bool,
};

export default QhPagination;
