import _ from 'lodash'
import numeral from 'numeral'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import {
  Box,
  Flex,
  Image,
  Heading,
  Text
} from 'rebass'
import { config } from 'librarys/lightcone_v3';
import { TransferModal } from 'widgets/Dialog'
import utils from 'utils'

import Chart from 'bizcharts/lib/components/Chart';
import Geom from 'bizcharts/lib/components/Geom';
import Coord from 'bizcharts/lib/components/Coord';

import { Tabs, Table, Select, Checkbox, Menu, Dropdown } from 'antd';
const { TabPane } = Tabs;
const { Option } = Select;

let tokens = config.getTokens()
const symbols = tokens.map(token => token.symbol)

const renderChart = _.memoize((overview) => {
  return (
    <Chart
      width={200}
      height={200}
      data={overview}
      padding={[0, 0, 0, 0]}
      forceFit
    >
      <Coord type={"theta"} radius={1} innerRadius={0.6} />
      <Geom
        type="intervalStack"
        position="percent"
        color={['count', ['#1890ff', '#0064FF']]}
      >
      </Geom>
    </Chart>
  )
}, overview => `${overview[0].count}-${overview[1].count}`)

class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      filters: {
        tokenSymbol: ''
      },
      marketOptions: []
    }
  }

  componentDidMount() {
    // this.props.fetchBalances()
    this.props.fetchWalletBalances()
  }

  onChangeTab (key) {
    if (key === 'dex') {
      // TODO: test
      // this.props.fetchBalances()
    } else {
      this.props.fetchWalletBalances()
    }
  }

  renderOverview () {
    let { dexAccount, balance, walletBalance, rate } = this.props
    let balanceTotal = _.reduce(balance.list, (total, item) => {
      let usd = utils.asLegalMoney(rate, Number(item.totalAmount), item.token.symbol, 'USD')
      return total + Number(usd)
    }, 0)
    let walletBalanceTotal = _.reduce(walletBalance.list, (total, item) => {
      let usd = utils.asLegalMoney(rate, Number(item.amount), item.symbol, 'USD')
      return total + Number(usd)
    }, 0)
    let total = balanceTotal + walletBalanceTotal

    const overview = [
      {
        item: "我的钱包",
        count: walletBalanceTotal,
        percent: total ? walletBalanceTotal / total * 100 : 0,
      },
      {
        item: "币币账户",
        count: balanceTotal,
        percent: total ? balanceTotal / total * 100: 0,
      },
    ];
    console.info(overview)
    return (
      <Flex>
        <Box size={200} mr={60} mb={90}>
          { renderChart(overview) }
        </Box>
        <Flex flexDirection="column" flex={1}>
          <Text color="#323232" fontSize={20} fontWeight="bolder">总资产估值</Text>
          <Flex mt={'5px'} alignItems="center">
            <Text color="#323232" fontSize={30} fontWeight="bolder">{total} USD</Text>
            <Text fontSize={20} ml={30} fontWeight="bolder">≈{utils.asLegalMoney(rate, total, 'USD', 'CNY')} CNY</Text>
          </Flex>
          <Text mt={'5px'} color="#323232">钱包地址: {dexAccount.address}</Text>
          <Flex mt={30} justifyContent="flex-start">
            <Box flex={1}>
              <Flex alignItems="center">
                <Box size={14} bg="#0064FF"></Box>
                <Text ml={'6px'} color="#969696">我的钱包资产估值</Text>
              </Flex>
              <Flex mt={'5px'} alignItems="center">
                <Text color="#323232" fontSize={14} fontWeight="bold">{walletBalanceTotal} USD</Text>
                <Text fontSize={14} ml={13}>≈{utils.asLegalMoney(rate, walletBalanceTotal, 'USD', 'CNY')} CNY</Text>
              </Flex>
            </Box>
            <Box flex={1}>
              <Flex alignItems="center">
                <Box size={14} bg="#1890ff"></Box>
                <Text ml={'6px'} color="#969696">币币账户资产估值</Text>
              </Flex>
              <Flex mt={'5px'} alignItems="center">
                <Text color="#323232" fontSize={14} fontWeight="bold">{balanceTotal} USD</Text>
                <Text fontSize={14} ml={13}>≈{utils.asLegalMoney(rate, balanceTotal, 'USD', 'CNY')} CNY</Text>
              </Flex>
            </Box>
          </Flex>
        </Flex>
      </Flex>
    )
  }

  renderFilters () {
    // let { balance, walletBalance } = this.props
    // let balanceSymbols = balance.list.map(b => b.token.symbol)
    // let walletBalanceSymbols = walletBalance.list.map(b => b.symbol)

    // let symbols = _.union(balanceSymbols, walletBalanceSymbols)
    return (
      <Flex alignItems="center" style={{position: 'absolute', right: 0, zIndex: 1000}}>
        <Checkbox style={{color: '#323232'}} onChange={() => {}}>隐藏小额代币</Checkbox>
        <Select
          // size="small"
          placeholder="全部币种"
          allowClear
          style={{ width: 140, color: '#323232' }}
          onChange={tokenSymbol => {
            this.setState(({ filters }) => {
              filters.tokenSymbol = tokenSymbol
              return { filters }
            })
          }}
        >
          {
            symbols.map(symbol => (
              <Option key={symbol} value={symbol}>{symbol}</Option>
            ))
          }
        </Select>
      </Flex>
    )
  }
  
  render() {
    let { filters, marketOptions } = this.state
    let { history, balance, walletBalance, markets: marketsStore, setCurrentTab, setCurrentMarket } = this.props
    let balances = balance.list
    let walletBalances = walletBalance.list

    if (filters.tokenSymbol) {
      balances = balances.filter(b => b.token.symbol === filters.tokenSymbol)
      walletBalances = walletBalances.filter(b => b.symbol === filters.tokenSymbol)
    }

    const walletColumns = [
      {
        title: '代币名称',
        dataIndex: 'tokenId',
        key: 'tokenId',
        render: (unused, record) => {
          return (
            <Flex>
              <Text>{record.symbol}</Text>
              <Text color="#969696">({record.name})</Text>
            </Flex>
          )
        },
      },
      {
        title: '余额',
        dataIndex: 'amount',
        key: 'amount',
      },
      {
        align: 'center',
        title: `操作`,
        dataIndex: '',
        key: 'ops',
        width: 140,
        render: (unused, record) => (
          <Flex>
            <Text color="#0078FF" mr={"6px"}
              sx={{ cursor: 'pointer' }}
              onClick={() => TransferModal({
                initialSymbol: record.symbol,
                initialType: 'deposit',
              })}
            >
              资金划转
            </Text>
            <Text color="#0078FF"
              sx={{ cursor: 'pointer' }}
              onClick={async () => {
                history.push({ pathname: `/mine/transfer`, data: {
                  initialSymbol: record.symbol,
                  initialSide: 'WITHDRAW',
                }})
              }}
            >
              资金记录
            </Text>
          </Flex>
        )
      }
    ]

    const dexColumns = [
      {
        title: '代币名称',
        dataIndex: 'tokenId',
        key: 'tokenId',
        render: (unused, record) => {
          return (
            <Flex>
              <Text>{record.token.symbol}</Text>
              <Text color="#969696">({record.token.name})</Text>
            </Flex>
          )
        },
      },
      {
        title: '可用余额',
        dataIndex: 'availAmount',
        key: 'availAmount',
      },
      {
        title: '挂单金额',
        dataIndex: 'frozenAmount',
        key: 'frozenAmount',
      },
      {
        title: '总计(可用+挂单)',
        dataIndex: 'totalAmount',
        key: 'totalAmount',
      },
      {
        align: 'center',
        title: `操作`,
        dataIndex: '',
        key: 'ops',
        width: 180,
        render: (unused, record) => (
          <Flex>
            <Dropdown overlay={
              <Menu onClick={({key: pair}) => {
                let [base, quote] = pair.split('-')
                setCurrentTab(quote)
                let currentMarket = _.find(marketsStore.markets, {market: pair})
                setCurrentMarket(currentMarket)
                history.push({ pathname: `/`})
              }}>
                {
                  marketOptions.map((menu) => (
                    <Menu.Item key={menu.market}>
                      <Text fontSize={[0, 0, 0]} color='hint'>{ menu.market }</Text>
                    </Menu.Item>
                  ))
                }
              </Menu>
              }
              placement="bottomCenter" overlayClassName="nav-menu"
            >
              <Text color="#0078FF" mr={"6px"} sx={{ cursor: 'pointer' }} onMouseEnter={() => {
                let markets = config.getMarkets().filter(market => market.baseToken === record.token.symbol || market.quoteToken === record.token.symbol)
                this.setState({marketOptions: markets})
              }}>交易</Text>
            </Dropdown>
            <Text color="#0078FF" mr={"6px"}
              sx={{ cursor: 'pointer' }}
              onClick={() => TransferModal({
                initialSymbol: record.token.symbol,
                initialType: 'withdraw',
              })}
            >
              资金划转
            </Text>
            <Text color="#0078FF"
              sx={{ cursor: 'pointer' }}
              onClick={async () => {
                history.push({ pathname: `/mine/transfer`, data: {
                  initialSymbol: record.token.symbol,
                  initialSide: 'DEPOSIT',
                }})
              }}
            >
              资金记录
            </Text>
          </Flex>
        )
      }
    ]

    return (
      <>
        { this.renderOverview() }
        <Box style={{position: 'relative'}}>
          { this.renderFilters() }
          <Tabs className="balance-tab" onChange={(key) => this.onChangeTab(key)} type="card">
            <TabPane tab="我的钱包" key="wallet">
              <Table rowKey="symbol" columns={walletColumns} dataSource={walletBalances}
                className="mine-table"
                pagination={false}
                locale={{emptyText: '暂无数据'}}
              />
            </TabPane>
            <TabPane tab="币币账户" key="dex">
              <Table rowKey="tokenId" columns={dexColumns} dataSource={balances}
                className="mine-table"
                pagination={false}
                locale={{emptyText: '暂无数据'}}
              />
            </TabPane>
          </Tabs>
        </Box>
      </>
    )
  }
}

const mapStateToProps = ({ dexAccount, balance, walletBalance, rate, markets }) => {
  return {
    dexAccount,
    balance,
    walletBalance,
    rate,
    markets
  };
};

const mapDispatchToProps = ({ balance, walletBalance, transfer, markets }) => {
  return {
    fetchBalances: balance.fetchBalances,
    fetchWalletBalances: walletBalance.fetchAllBalances,
    setCurrentTab: markets.setCurrentTab,
    setCurrentMarket: markets.setCurrentMarket,
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget))