import _ from 'lodash'
import React from 'react';
import { connect } from 'react-redux';
import numeral from 'numeral'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import { Table, Button, Input, Icon, AutoComplete } from 'antd';
const { Option } = AutoComplete;

class Pair extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fuzzy: '',
    }
  }

  componentDidMount() {
  }
  
  renderOption(item) {
    return (
      <Option key={item.category} text={item.category}>
        <div className="global-search-item">
          <span className="global-search-item-desc">
            Found {item.query} on
            <a
              href={`https://s.taobao.com/search?q=${item.query}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              {item.category}
            </a>
          </span>
          <span className="global-search-item-count">{item.count} results</span>
        </div>
      </Option>
    );
  }

  onChange(pagination, filters, sorter, extra) {
    let { order = 'market', field = 'descend' } = sorter
    this.props.updateFilterCond({
      sort: order,
      sortDirection: order === 'descend' ? 'desc' : 'asc'
    })
  }

  render() {
    let { markets, setCurrentTab, setCurrentMarket, starMarket } = this.props
    let { fuzzy } = this.state
    let pairs = _.chain(markets.currentMarkets)
                  .filter(({market}) => _.includes(_.upperCase(market), _.upperCase(fuzzy)))
                  .value()

    const columns = [
      {
        align: 'left',
        title: '',
        key: 'star',
        width: 20,
        render: (unused, record) => {
          let haveStar = _.find(markets.stars, {market: record.market})
          return (<Icon type="star" theme="filled"  style={{ fontSize: '14px', color: haveStar ? "#0064FF": "#3c3c50" }} />)
        },
        onCell: (record, rowIndex) => {
          return {
            onClick: event => {
              event.stopPropagation()
              starMarket(record)
            }
          }
        }
      },
      {
        align: 'left',
        title: '交易对',
        dataIndex: 'market',
        width: 80,
        sorter: (a, b) => a.market - b.market,
        render: (unused, record) => (<Flex>{record.baseToken}<Text color="hint">/{record.quoteToken}</Text></Flex>),
      },
      {
        align: 'right',
        title: '最新价格',
        dataIndex: 'latest',
        width: 100,
        sorter: (a, b) => a.name - b.name,
        render: (latest) => (<Text>{latest}</Text>),
      },
      {
        align: 'right',
        title: ' 24H涨跌',
        dataIndex: 'variation',
        sorter: (a, b) => a.variation - b.variation,
        render: (variation) => (<Text color={variation >= 0 ? 'success' : 'failure'}>{numeral(variation).format('+0.00%')}</Text>),
      },
    ];

    return (
      <Box {...this.props} variant='noScrollbar' overflowY='auto'>
        <Flex alignItems='center' justifyContent='space-around'>
          {
            markets.tabs.map(tab => (
              <Flex key={tab} width={90} height={40} alignItems='center' justifyContent='center' sx={{
                borderBottom: '2px solid',
                borderColor: tab === markets.currentTab ? 'blue': 'background',
                cursor: 'pointer'
              }} onClick={() => setCurrentTab(tab)}>
                <Text fontSize={0} fontWeight={'bold'}>{tab}</Text>
              </Flex>
            ))
          }
        </Flex>
        <Box px={14} pt={18} pb={12}>
          {/* <AutoComplete
            className="pairs-search"
            dataSource={pairs}
            onSelect={pair => {
              let [base, quote] = pair.split('-')
              setCurrentTab(quote)
              let currentMarket = _.find(markets.markets, {market: pair})
              setCurrentMarket(currentMarket)
            }}
            onSearch={value => this.setState({ fuzzy: value })}
            optionLabelProp="text"
          >
            <Input.Search
              className="pairs-search"
              placeholder="搜索"
              onSearch={value => this.setState({ fuzzy: value })}
              style={{ height: 36, color: 'white' }}
            />
          </AutoComplete> */}
          <Input.Search
            className={`pairs-search ${fuzzy && 'has-content'}`}
            placeholder="搜索"
            style={{ height: 36, color: 'white' }}
            allowClear={true}
            onChange={e => this.setState({ fuzzy: e.currentTarget.value })}
          />
        </Box>
        <Box px={14}>
          <Table columns={columns} dataSource={pairs} align="right"
            className="exchange-table"
            pagination={false}
            onRow={(record, rowIndex) => {
              return {
                onClick: event => {
                  setCurrentMarket(record)
                }
              }
            }}
            locale={{emptyText: '暂无数据'}}
            onChange={(...params) => this.onChange(...params)}
          />
        </Box>
      </Box>
    )
  }
}

const mapStateToProps = ({markets}) => {
  return {
    markets
  };
};

const mapDispatchToProps = ({markets}) => {
  return {
    setCurrentTab: markets.setCurrentTab,
    setCurrentMarket: markets.setCurrentMarket,
    starMarket: markets.starMarket,
    updateFilterCond: markets.updateFilterCond
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Pair);