import React from 'react';
import _ from 'lodash';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Menu, Dropdown, Button } from 'antd';
import { BillPreCheck } from './Common'
import { renderIf } from 'utils';
import metamaskLogo from 'metamask.png';
import wedexLogo from 'icons/wedexLogo.svg';
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import { UpsertPasswordModal } from 'widgets/Dialog'

class TopNav extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activitis: [
        { title: '关于WEDEX9月26日升级公告', link: 'http://www.baidu.com' },
        { title: '10月交易大赛赢百万糖果', link: 'http://www.baidu.com' },
        { title: '关于WEDEX9月26日升级公告', link: 'http://www.baidu.com' },
      ]
    }
  }

  componentDidMount() {
  }

  login() {
    this.billPreCheck.check()
  }

  async changePassword () {
    this.billPreCheck.checkSignedIn()
    await UpsertPasswordModal()
  }
  
  async clearStore () {
    this.props.clearAccountInfo()
    this.props.history.push('/')
  }

  renderNav () {
    const menus = {
      mine: [
        { title: '我的资产', link: '/mine/balance' },
        { title: '资金划转记录', link: '/mine/transfer' },
        { title: '币币交易记录', link: '/mine/order' },
        { title: 'API Key', link: '/mine/apikey' },
        { title: '中奖记录', link: '/mine/award' },
        { title: '修改交易密码', callback: () => this.changePassword() },
        { title: '清空浏览器缓存', callback: () => this.clearStore() },
      ],
      activity: [
        { title: '最新公告', newWindow: true, link: 'https://wedex.io/support/announcement' },
        { title: '新手教程', newWindow: true, link: 'https://wedex.io/support/guide' },
        { title: '常见问题', newWindow: true, link: 'https://wedex.io/support/faq' },
        // { title: '上币申请', newWindow: true, link: '' },
        { title: '关于我们', newWindow: true, link: 'https://wedex.io/support/about' },
      ]
    }
    const genMenu = subMenus => (
      <Menu onClick={({key: index}) => {
        let menu = subMenus[index]
        if (menu.link) {
          // newWindow
          // TODO: 
          if (menu.newWindow) {
            window.open(menu.link, '_blank')
          } else {
            this.props.history.push(menu.link)
          }
        } else if (menu.callback) {
          menu.callback()
        }
      }}>
      {
        subMenus.map((menu, index) => (
          <Menu.Item key={index}>
            <Text fontSize={[0, 0, 0]} color='hint'>{ menu.title }</Text>
          </Menu.Item>
        ))
      }
      </Menu>
    )

    let { address, passwordHash, state } = this.props.dexAccount
    let { isDesiredBrowser, isDesiredNetwork } = this.props.metaMask
    let signined = address && passwordHash
    let accountCreating = ENUM_ACCOUNT_STATUS.CREATING.equals(state)

    return (
      <Flex alignItems='center'>
        {renderIf(isDesiredNetwork && signined && !accountCreating)(
          <Dropdown overlay={genMenu(menus.mine)} placement="bottomCenter" overlayClassName="nav-menu">
            <Flex px={25} height={40} alignItems='center'>
              <Image src={metamaskLogo} width={[25, 25, 25]} height={[25, 25, 25]} />
              <Box pl={3} fontSize={`13px`} fontWeight="bold">{`${_.take(address, 6).join('')}...${_.takeRight(address, 4).join('')}`}</Box>
            </Flex>
          </Dropdown>
        )}
        {renderIf(!isDesiredBrowser || !isDesiredNetwork || !signined || accountCreating)(
          <Flex px={25} height={40} alignItems='center' sx={{ cursor: 'pointer' }}
            onClick={() => this.login()}
          >
            {/* <Box pl={3} fontSize={`13px`} fontWeight="bold">{!isDesiredBrowser ? 'MetaMask登录' : !isDesiredNetwork ? '请选择主网' : accountCreating ? '创建中' : 'MetaMask登录'}</Box> */}
            <Box pl={3} fontSize={`13px`} fontWeight="bold">{accountCreating ? '创建中' : 'MetaMask登录'}</Box>
          </Flex>
        )}
        <Box height={40} lineHeight={'40px'} px={25} fontSize={`13px`} fontWeight="bold">最新活动</Box>
        <Dropdown overlay={genMenu(menus.activity)} placement="bottomCenter" overlayClassName="nav-menu">
          <Box height={40} lineHeight={'40px'} pl={25} fontSize={`13px`} fontWeight="bold">帮助中心</Box>
        </Dropdown>
        {/* <Dropdown overlay={genMenu(menus.activity)} placement="bottomCenter" overlayClassName="nav-menu">
          <Box height={40} lineHeight={'40px'} px={25} fontSize={`13px`} fontWeight="bold">简体中文</Box>
        </Dropdown> */}
        <BillPreCheck ref={ref => this.billPreCheck = ref}></BillPreCheck>
      </Flex>
    )
  }

  render() {
    return (
      <Box>
        <Flex variant='topNav' px={15}>
          <Flex width={177} alignItems='center' justifyContent='start' variant="clickable" onClick={() => {
            this.props.history.push(`/`)
          }}>
            <Image src={wedexLogo} width={150} />
            {/* <Text color="white" ml={2} fontSize={3}>WEDEX</Text> */}
          </Flex>
          <Flex alignItems='center' flex={1}>
            {/* {
              _.map(this.state.activitis, (activity, index) => (
                <Text key={index} mr={40} fontSize={[0, 0, 0]} color='hint'>{ activity.title }</Text>
              ))
            } */}
          </Flex>
          { this.renderNav() }
        </Flex>
        <Box as='hr' variant='divider.topNav'
          // sx={{
          // }}
        />
      </Box>
    )
  }
}

const mapStateToProps = ({ dexAccount, metaMask }) => {
  return {
    dexAccount,
    metaMask
  };
};

const mapDispatchToProps = ({ dexAccount }) => {
  return {
    clearAccountInfo: dexAccount.clearAccountInfo
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(TopNav))