import moment from 'moment'
import _ from 'lodash'
import React from 'react';
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import { Table, Input, Icon } from 'antd';
import awardIcon from 'icons/award.png';
import wedexLogoVertical from 'icons/wedexLogoVertical.png';

class Widget extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
    this.setState({
    })
  }

  render() {
    let { appState: {awardInfo}, dexAccount, closeAward, markRead } = this.props
    if (!awardInfo.visible) return <></>
    return (
      <Flex variant="flexCenter" sx={{
        position: 'fixed',
        top: 0,
        bottom: 0,
        left: 0,
        right: 0,
        margin: 'auto',
        backgroundImage: `url(${awardIcon})`,
        backgroundSize: 'contain',
        backgroundPosition: 'center',
        width: 375 * 1.4,
        height: 515 * 1.4,
        flexDirection: 'column',
        justifyContent: 'flex-start',
        zIndex: 999
      }} >
        {/* <Image src={awardIcon} width={300} /> */}
        <Image mt={262} src={wedexLogoVertical} size={45} />
        <Text fontSize={14} mt={'8px'} color="black" fontWeight={700}>恭喜您获得彩蛋</Text>
        <Text fontSize={20} color="red" fontWeight={700}>{awardInfo.amount}{awardInfo.unit}</Text>
        <Flex variant="flexCenter" color="hint" sx={{
          borderTop: '1px dashed #646478',
          flexDirection: 'column'
        }}>
          <Text mt={2}>奖金将在24小时内转入您的钱包</Text>
          <Text mt={0}>{`${_.take(dexAccount.address, 10).join('')}......${_.takeRight(dexAccount.address, 10).join('')}`}</Text>
          <Text mt={0}>获奖时间:{moment(awardInfo.createdAt).format('YYYY-MM-DD HH:mm:ss')}</Text>
        </Flex>
        <Box sx={{
            position: 'absolute',
            right: 100,
            top: 100,
            border: '1px solid #646478',
            borderRadius: '50%',
            size: 30,
            // padding: '3px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            cursor: 'pointer'
          }} onClick={async () => {
            await markRead([awardInfo.id])
            closeAward()
          }}>
          <Icon type="close" style={{ fontSize: '15px', width: '15px', height: '15px'}}/>
        </Box>
      </Flex>
    )
  }
}

const mapStateToProps = ({appState, dexAccount}) => {
  return {
    appState: appState,
    dexAccount: dexAccount,
  };
};

const mapDispatchToProps = ({appState, award}) => {
  return {
    closeAward: appState.closeAward,
    markRead: award.markRead
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget);