import _ from 'lodash'
import numeral from 'numeral'
import { config } from 'librarys/lightcone_v3'
import api from 'api'

const initState = {
  list: [],
  latestPrice: 0,
  lastPrice: 0,
  variation: 0,
  page: 1,
  pageSize: 10
}

function formatFromWEI (trades, currentMarket) {
  let { baseToken: baseSymbol, quoteToken: quoteSymbol, pricePrecision, amountPrecision, totalPrecision } = currentMarket

  trades = trades.map(trade => {
    trade.side = _.toUpper(trade.side)
    trade.timestamp = Number(trade.timestamp)
    trade.price = Number(trade.price).toFixed(pricePrecision)
    trade.size = config.fromWEI(baseSymbol, Number(trade.size), amountPrecision)
    return trade
  })

  return trades
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload })
  },
  effects: dispatch => ({
    async fetchTrade (payload, { markets, trade: tradeStore }) {
      if (!markets.currentMarket) return
      let { market: marketPair } = markets.currentMarket

      var trades = await api.getTrade(marketPair, 50);
      if (!trades || trades.length === 0) return
      trades = formatFromWEI(trades, markets.currentMarket)

      let lastPrice = tradeStore.latestPrice
      let latestPrice = trades[0].price
      let variation = 0
      if (lastPrice && latestPrice) {
        variation = (latestPrice - lastPrice) / lastPrice
      }

      this.update({
        list: trades,
        lastPrice,
        latestPrice,
        variation
      })

      // 每次全量获取完 trade 后，重新订阅
      dispatch.socket.subscribe([ `trade&${marketPair}` ])
    },
    async incrementalUpdate (trades, { trade: tradeState, markets }) {
      let { market: marketPair } = markets.currentMarket
      if (!marketPair) return

      trades = formatFromWEI(trades, markets.currentMarket)

      let { list } = tradeState
      list = list.concat(trades)

      // TODO 优化：根据 tradeid 合并。
      // 顺序应该有规律。每次只把最新的添加到队列头部就行，没必要每次全部放在一起排序。
      this.update({
        list: _.orderBy(list, ['timestamp'], ['desc']),
      })
    },
  })
}