import numeral from 'numeral';
import React from 'react';
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Table, Dropdown, Menu, Icon } from 'antd'
import utils from 'utils'
import { Svg } from 'components'
import depthAllIcon from 'icons/depthAll.svg'
import depthSellIcon from 'icons/depthSell.svg'
import depthBuyIcon from 'icons/depthBuy.svg'

const tableHeaderHeight = 30

const depthTypes = [
  { key: 'all', icon: depthAllIcon },
  { key: 'sell', icon: depthSellIcon },
  { key: 'buy', icon: depthBuyIcon },
]
class Depth extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tableHeight: 400,
      activeType: depthTypes[0]
    }
  }

  onWindowResize = function () {
    const windowHeight = document.body.clientHeight

    let tableHeight = (windowHeight - (50 + 3 * 2 + 40 + 30 + 30)) / 2
    this.setState({tableHeight})
  }.bind(this)

  componentDidMount() {
    window.addEventListener('resize', this.onWindowResize);
    this.onWindowResize()
  }
  
  componentWillUnmount() {
    window.removeEventListener('resize', this.onWindowResize);
  }

  renderPrecisions () {
    let precisions = this.props.precisions()
    return (
      <Menu onClick={({key}) => {
        let { markets, setLevel } = this.props
        let precision = Number(key)
        let level = markets.currentMarket.pricePrecision - precision
        setLevel(level)
      }}>
      {
        precisions.map((precision) => (
          <Menu.Item key={precision}>
            <Text fontSize={[0, 0, 0]} color='hint'>{ precision }位小数</Text>
          </Menu.Item>
        ))
      }
      </Menu>
    )
  }

  render() {
    let { markets, depth, trade: { latestPrice, variation }, currentPrecision, rate } = this.props
    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    const columns = [
      {
        align: 'left',
        title: (col) => `价格(${currentMarket.quoteToken})`,
        dataIndex: 'price',
        render: (price, row) => (
          <Text color={row.type === 'ask' ? "failure" : "success"}>{price}</Text>
        )
      },
      {
        align: 'right',
        title: (col) => `数量(${currentMarket.baseToken})`,
        dataIndex: 'size',
        width: 105,
        render: size => (
          <Text>{size}</Text>
        )
      },
      {
        align: 'right',
        title: (col) => `累计(${currentMarket.quoteToken})`,
        dataIndex: 'volume',
        width: 105,
        render: volume => (
          <Text>{volume}</Text>
        )
      },
    ];

    const onRowEvent = record => {
      return {
        onClick: event => {
          this.props.updatePlaceOrder({
            buyPrice: record.price,
            buyAmount: null,
            buyTotal: null,
            marketBuyTotal: null,
          })
          this.props.updatePlaceOrder({
            sellPrice: record.price,
            sellAmount: null,
            sellTotal: null,
            marketSellAmount: null,
          })
        }
      }
    }

    let { tableHeight, activeType } = this.state
    let { key: depthType } = activeType
    return (
      <Box width={320}>
        <Flex px={14} height={40} alignItems="center" justifyContent="flex-start">
          <Text fontSize={0} fontWeight="bolder">交易深度</Text>
          <Flex ml={'5px'}>
            {
              depthTypes.map(type => (
                <Flex key={type.key} width={16} height={14} mx={'4px'} variant="flexCenter" sx={{
                    borderRadius: '2px',
                    border: `1px solid ${this.state.activeType === type ? '#0064FF' : '#32374b'}`,
                    cursor: 'pointer',
                  }}
                  onClick={() => {
                    this.setState({ activeType: type })
                    this.props.scrollSellTable()
                  }}
                >
                  <Image width={10} height={6} src={type.icon} />
                </Flex>
              ))
            }
          </Flex>
          <Box flex={1}></Box>
          <Dropdown overlay={this.renderPrecisions()} placement="bottomCenter" overlayClassName="nav-menu">
            <Flex height={40} lineHeight={'40px'} alignItems="center">{currentPrecision()}位小数<Icon type="caret-down" /></Flex>
          </Dropdown>
        </Flex>
        <Flex height={30} alignItems="center" px={14}>
          <Box px={`2px`} fontSize="12" lineHeight={`20px`} color="#646478" flex={1}>{`价格(${currentMarket.quoteToken})`}</Box>
          <Box px={`2px`} fontSize="12" lineHeight={`20px`} color="#646478" width={105} textAlign="right">{`数量(${currentMarket.baseToken})`}</Box>
          <Box px={`2px`} fontSize="12" lineHeight={`20px`} color="#646478" width={105} textAlign="right">{`累计(${currentMarket.quoteToken})`}</Box>
        </Flex>
        {
          utils.renderIf(depthType === 'all' || depthType === 'sell')(
            <Box px={14}>
              <Table px={14} columns={columns} dataSource={depth.asks} onRow={onRowEvent} align="right"
                className="exchange-table sell-table"
                pagination={false}
                scroll={{ y: depthType === 'all' ? tableHeight : tableHeight * 2 }}
                style={{ height: depthType === 'all' ? tableHeight : tableHeight * 2 }}
                showHeader={false}
                locale={{emptyText: '暂无数据'}}
              />
            </Box>
          )
        }
        <Flex px={14} height={30} fontWeight="bold" bg="backgroundTint" alignItems="center">
          <Text mr={'8px'} fontSize={'18px'} color={variation >= 0 ? 'success' : 'failure'}>{latestPrice}</Text>
          <Svg color={variation >= 0 ? '#50BE8c' : '#F0505A'} icon={variation >= 0 ? 'icon-arrow-up' : 'icon-arrow-down'} size="25px"></Svg>
          <Text ml={'8px'} fontSize={'18px'} color="#3c3c50">≈$ {utils.asLegalMoney(rate, latestPrice, currentMarket.quoteToken, 'USD')}</Text>
        </Flex>
        {
          utils.renderIf(depthType === 'all' || depthType === 'buy')(
            <Box px={14}>
              <Table px={14} columns={columns} dataSource={depth.bids} onRow={onRowEvent} align="right" 
                className="exchange-table"
                pagination={false}
                scroll={{ y: depthType === 'all' ? tableHeight : tableHeight * 2 }}
                showHeader={false}
                // style={{ height: tableHeight - tableHeaderHeight}}
                locale={{emptyText: '暂无数据'}}
              />
            </Box>
          )
        }
      </Box>
    )
  }
}

const mapStateToProps = ({ markets, depth, trade, rate }) => {
  return {
    markets,
    depth,
    trade,
    rate,
    precisions: () => {
      let market = markets.currentMarket
      if (!market) return []

      let { pricePrecision, maxLevel } = market
      return _.range(pricePrecision, pricePrecision - maxLevel)
    },
    currentPrecision: () => {
      if (!markets.currentMarket) return 0
      return markets.currentMarket.pricePrecision - depth.level
    }
  };
};

const mapDispatchToProps = ({ depth, placeOrder }) => {
  return {
    setLevel: depth.setLevel,
    scrollSellTable: depth.scrollSellTable,
    updatePlaceOrder: placeOrder.update
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Depth);