import _ from 'lodash'
import api from 'api'
import { Account, common, config } from 'librarys/lightcone_v3';
import utils from 'utils'
const BigNumber = require('bignumber.js');

const initState = {
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload })
  },
  effects: dispatch => ({
    async checkAward (payload, { award, dexAccount, appState }) {
      if (!dexAccount.address) return
      if (appState.awardInfo.visible) return

      let { latestAward } = award
      let latestAwardBillSn = latestAward && latestAward.billSn

      let resp = await api.getUserBillEggPrizeList({
        // address: '0xa4ac2bb3885ff3bf82e99cc2b442a379e3627ef7',
        address: dexAccount.address,
        pageSize: 1,
        page: 1,
        sort: 'id',
        sortDirection: 'asc',
        read: false
      })
      let newAward = resp.data.rows[0]
      if (!newAward) return
      
      let { id, billSn, money, unit, createdAt } = newAward

      // TODO: 中奖
      dispatch.appState.showEggAward({
        id,
        amount: money,
        unit,
        createdAt
      })
    },
    async markRead (ids, { dexAccount }) {
      if (!dexAccount.address) return

      await api.postUserReadBillEggPrizeList({
        // address: '0xa4ac2bb3885ff3bf82e99cc2b442a379e3627ef7',
        address: dexAccount.address,
        ids
      })
    }
  })
}