import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Row, Col, Button } from 'antd';
import colors from 'colors';
import { LOGGED_IN } from 'redux/actions/DexAccount';

import {
  showDepositModal,
  showWithdrawModal
} from 'redux/actions/ModalManager';

class MyBalancesOverview extends React.Component {
  setDepositModalVisible(depositModalVisible) {
    this.setState({ depositModalVisible });
  }

  setWithdrawModalVisible(withdrawModalVisible) {
    this.setState({ withdrawModalVisible });
  }

  render() {
    const { dexAccount } = this.props;
    const submitButtonStyle = {
      color: colors.textHighContrast,
      borderStyle: 'none',
      height: '38px',
      borderRadius: '0px',
      fontWeight: '600'
    };

    const submitBuyButtonStyle = {
      ...submitButtonStyle,
      backgroundColor: colors.buyPrimary
    };

    const submitSellButtonStyle = {
      ...submitButtonStyle,
      backgroundColor: colors.sellPrimary
    };

    let buttons;
    // TODO: needs refactor
    if (
      typeof window.wallet !== 'undefined' &&
      !!dexAccount &&
      !!dexAccount.account
    ) {
      if (
        dexAccount.account.keyPairCipherText &&
        dexAccount.account &&
        dexAccount.account.accountId
      ) {
        buttons = (
          <div>
            <div
              style={{
                marginTop: '30px',
                color: colors.textLowContrast,
                fontWeight: 400,
                fontSize: '0.9rem'
              }}
            >
              <Button
                className={'deposit-button'}
                block
                style={submitBuyButtonStyle}
                onClick={() => {
                  this.props.showDepositModal(true);
                }}
                disabled={!this.props.metaMask.isDesiredNetwork}
              >
                DEPOSIT
              </Button>
            </div>

            <div
              style={{
                marginTop: '30px',
                color: colors.textLowContrast,
                fontWeight: 400,
                fontSize: '0.9rem'
              }}
            >
              <Button
                className={'withdraw-button'}
                block
                style={submitSellButtonStyle}
                onClick={() => {
                  this.props.showWithdrawModal(true);
                }}
                disabled={
                  this.props.dexAccount.account.state !== LOGGED_IN ||
                  !this.props.metaMask.isDesiredNetwork
                }
              >
                WITHDRAW
              </Button>
            </div>
          </div>
        );
      }
    }

    return (
      <div
        style={{
          paddingTop: '30px',
          paddingLeft: '30px',
          paddingRight: '30px',
          paddingBottom: '30px'
        }}
      >
        <div
          style={{
            lineHeight: '53px',
            height: '53px',
            color: colors.textMedContrast,
            fontWeight: 700,
            fontSize: '1.4rem',
            userSelect: 'none'
          }}
        >
          Balance Overview
        </div>
        <div
          style={{
            marginTop: '30px',
            color: colors.textLowContrast,
            fontWeight: 400,
            fontSize: '0.9rem',
            userSelect: 'none'
          }}
        >
          Total
        </div>
        <div
          style={{
            marginTop: '10px',
            color: colors.textMedContrast,
            fontWeight: 400,
            fontSize: '1.4rem'
          }}
        >
          -
        </div>
        {buttons}
      </div>
    );
  }
}

const mapStateToProps = state => {
  const { layoutManager, balances, dexAccount, metaMask } = state;
  return { layoutManager, balances, dexAccount, metaMask };
};

const mapDispatchToProps = dispatch => {
  return {
    showDepositModal: show => dispatch(showDepositModal(show)),
    showWithdrawModal: show => dispatch(showWithdrawModal(show))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyBalancesOverview);
