import _ from 'lodash'
import React, { forwardRef, useEffect, useState, useImperativeHandle, useRef } from 'react'
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import utils from 'utils';
import { Input, Popover, Icon, InputNumber } from 'antd';
const InputGroup = Input.Group;

const CoinInput = ({
  disabled = false,
  value,
  min,
  max,
  precision,
  error,
  onChange,
  approximate,
  unit
}) => {
  const [focused, setFocused] = useState(false)

  const content = (
    <div>
      <p>{error}</p>
    </div>
  )
  return (
    <Popover content={content} trigger={error ? "hover" : 'focus' } overlayStyle={{
      display: error ? undefined : 'none'
    }}>
      <InputGroup compact style={{
        display: 'flex',
        borderRadius: '3px',
        webkitBoxShadow: (focused || error) ? `0 0 0 1px ${error ? '#F0505A' : 'rgba(24, 144, 255, 0.2)'}` : 'none'
      }}>
        <Input
          disabled={disabled}
          className="coin-input"
          precision={precision}
          value={value}
          onKeyPress={(e)=>{
            // if (e.key === 'Backspace' || e.key === 'Delete') return
            if (!/^[0-9a-zA-Z]+$/.test(e.key)) return

            let value
            let { selectionStart, selectionEnd } = e.currentTarget
            if (selectionStart !== selectionEnd) {
              value = e.target.value
              value = Number(value.slice(0, selectionStart) + e.key + value.slice(selectionEnd))
            } else {
              value = Number(e.target.value + e.key)
            }

            if (!_.isNumber(value) || isNaN(value)) {
              e.preventDefault()
              return
            }

            // fix: 这里必须用 e.target.value + e.key，因为必须直接拿字符串判断。否则 0.0100000000，转成数字后，后面的 0 会被舍弃，用户就可以一直输入。
            if (utils.getPrecision(e.target.value + e.key) > precision) {
              e.preventDefault()
              return
            }
          }}
          onChange={onChange}
          onBlur={() => setFocused(false)}
          onFocus={() => setFocused(true)}
          style={{minWidth: 30, flex: 1}}
        />
        {
          utils.renderIf(approximate)(
            <Input
              value={`≈${approximate}`} style={{
                padding: 0,
                color: '#3c3c50',
                textAlign: 'left',
                pointerEvents: 'none',
                backgroundColor: '#0A0F1E',
                border: 0,
                minWidth: 30,
                flex: 1,
                height: '32px'
              }}
              disabled
            />
          )
        }
        <Input value={unit} style={{
            paddingLeft: 0,
            width: 55,
            border: 0,
            color: '#646478',
            pointerEvents: 'none',
            backgroundColor: "#0A0F1E",
            textAlign: 'right',
            height: '32px'
          }}
          disabled
        />
      </InputGroup>
    </Popover>
  )
}

const mapStateToProps = state => {
  return {
  };
};

const mapDispatchToProps = dispatch => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CoinInput);