import _ from 'lodash'
import React from 'react';
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import { Table, Input, Icon } from 'antd';
import utils from 'utils';

const Widget = ({errors, items}) => {
  return (
    <Box>
      <Text fontSize={14} color="#969696" ml={'6px'}>密码必须包含以下内容:</Text>
      {
        _.map(items, item => {
          let error = !!_.includes(errors, item)
          return (
            <Flex key={item} alignItems="center">
              {
                utils.renderIf(error)(
                  <Icon style={{color: "#F0505A"}} type="close" />
                )
              }
              {
                utils.renderIf(!error)(
                  <Icon style={{color: "#50BE8c"}} type="check" />
                )
              }
              <Text lineHeight={'30px'} fontSize={14} line color="#969696" ml={'6px'}>{item}</Text>
            </Flex>
          )
        })
      }
    </Box>
  )
}

const mapStateToProps = state => {
  return {
  };
};

const mapDispatchToProps = dispatch => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Widget);