import moment from 'moment';
import React from 'react';
import { connect } from 'react-redux';
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Table, Input, Icon } from 'antd';

class Trade extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tableHeight: 100
    }
  }

  onWindowResize = function () {
    let tableDom = document.getElementsByClassName('trade-panel')[0]
    if (!tableDom) return
    this.setState({
      tableHeight: tableDom.offsetHeight
    })
  }.bind(this)

  componentDidMount() {
    window.addEventListener('resize', this.onWindowResize);
    this.onWindowResize()
    // fix: trade的高度有问题，太小。是因为此处第一次执行时没加载完，获取的 dom 高度不对。
    setTimeout(() => this.onWindowResize(), 1000)
  }
  
  componentWillUnmount() {
    window.removeEventListener('resize', this.onWindowResize);
  }

  render() {
    let { trade, markets } = this.props
    let { tableHeight } = this.state

    let currentMarket = markets.currentMarket
    if (!currentMarket) return <></>

    const columns = [
      {
        align: 'left',
        title: `价格(${currentMarket.quoteToken})`,
        dataIndex: 'price',
        width: 96,
        render: (price, row) => (
          <Text color={row.side === 'BUY' ? "success" : "failure"}>{row.price}</Text>
        )
      },
      {
        align: 'right',
        title: `数量(${currentMarket.baseToken})`,
        dataIndex: 'size',
        width: 96,
      },
      {
        align: 'right',
        title: '时间',
        dataIndex: 'timestamp',
        render: (unused, record) => {
          return (<Text>{moment(record.timestamp).format('HH:mm:ss')}</Text>)
        }
      },
    ];

    return (
      <Box {...this.props} className="trade-panel">
        <Flex height={40} alignItems="center" justifyContent="space-between">
          <Text fontSize={0} fontWeight="bold">实时成交</Text>
          {/* <Text fontSize={0} color='hint'>更多</Text> */}
        </Flex>
        <Table columns={columns} dataSource={trade.list} align="right"
          scroll={{ y: tableHeight - 72 }}
          className="exchange-table"
          pagination={false}
          locale={{emptyText: '暂无数据'}}
        />
      </Box>
    )
  }
}

const mapStateToProps = ({ trade, markets }) => {
  return {
    trade,
    markets
  };
};

const mapDispatchToProps = ({ trade }) => {
  return {
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Trade);