import { updateMyOrders } from 'redux/actions/MyOrders';
import { updateMyBalances } from 'redux/actions/MyBalances';
import { sleep } from 'api/LightconeAPI';

export const GET_DATA_FROM_LOCALSTORAGE = 'GET_DATA_FROM_LOCALSTORAGE';
export const GET_DATA_FROM_LOCALSTORAGE_SUCCEEDED =
  'GET_DATA_FROM_LOCALSTORAGE_SUCCEEDED';

export const CREATE_ACCOUNT = 'CREATE_ACCOUNT';
export const SEND_CREATE_REQUEST = 'SEND_CREATE_REQUEST';
export const SEND_CREATE_REQUEST_SUCCEEDED = 'SEND_CREATE_REQUEST_SUCCEEDED';
export const SEND_CREATE_REQUEST_FAILED = 'SEND_CREATE_REQUEST_FAILED';

export const CREATE_ON_CHAIN_PENDING = 'CREATE_ON_CHAIN_PENDING';
export const CREATE_ON_CHAIN_SUCCEEDED = 'CREATE_ON_CHAIN_SUCCEEDED';
export const CREATE_ON_CHAIN_FAILED = 'CREATE_ON_CHAIN_FAILED';

export const ENABLE_TOKENS = 'ENABLE_TOKENS';
export const CANCELED_ORDERS = 'CANCELED_ORDERS';
export const REMOVE_CANCELED_ORDERS = 'REMOVE_CANCELED_ORDERS';
export const UPDATE_PASSWORD = 'UPDATE_PASSWORD';

export const LOGOUT = 'LOGOUT';
export const CHANGE_ACCOUNT = 'CHANGE_ACCOUNT';

export const UPDATE_ACCOUNT = 'UPDATE_ACCOUNT';
export const UPDATE_REMEMBER_ME = 'UPDATE_REMEMBER_ME';

export const WALLET_UNCONNECTED = 'WALLET_UNCONNECTED';
export const UNCREATED = 'UNCREATED';
export const CREATING = 'CREATING';
export const CREATED = 'CREATED';
export const LOCKED = 'LOCKED';
export const LOGGED_IN = 'LOGGED_IN';

export function updateRememberMe(rememberMe) {
  return {
    type: UPDATE_REMEMBER_ME,
    payload: { rememberMe }
  };
}

export function updatePassword(password) {
  return {
    type: UPDATE_PASSWORD,
    payload: { password: password }
  };
}

export function updateAccount(account) {
  return {
    type: UPDATE_ACCOUNT,
    payload: { account: account }
  };
}

export function getDataFromLocalStorage(address) {
  return {
    type: GET_DATA_FROM_LOCALSTORAGE,
    payload: { address }
  };
}

export function canceledOrders(hash) {
  return {
    type: CANCELED_ORDERS,
    payload: {
      hash
    }
  };
}

export function removeCanceledOrders() {
  return {
    type: REMOVE_CANCELED_ORDERS,
    payload: {}
  };
}

export function logout() {
  return {
    type: LOGOUT,
    payload: {}
  };
}

export function changeAccount() {
  return dispatch => {
    dispatch({
      type: CHANGE_ACCOUNT,
      payload: {}
    });
  };
}

export function logoutAll() {
  return dispatch => {
    (async () => {
      try {
        dispatch(logout());
        await sleep(2000);

        dispatch(updateMyOrders([]));
        dispatch(updateMyBalances([]));
      } catch (error) {}
    })();
  };
}
