import _ from 'lodash'
import { takeLatest, takeEvery, all, call, put, select } from 'redux-saga/effects'
import { startup } from './StartupSagas'
import api from 'api'
import { push, replace } from 'connected-react-router'
import utils from 'utils'
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import { notification } from 'antd';

/* ------------- Types ------------- */

/* ------------- Sagas ------------- */

/* ------------- Connect Types To Sagas ------------- */


// 所有使用saga异步调用的函数，必须处理所有异常，否则抛出异常后会导致整个saga事件机制崩溃，程序无法正常使用
function safeWrap (func) {
  if (!func) {
    return () => {}
  }

  return function * (...args) {
    // console.tron.log({safeWrap: 'safeWrap', args})
    try {
      yield call(func, ...args)
    } catch (err) {
      console.info('saga err!!!', err)
      // console.tron.log(err)
    }
  }
}

function * onLocationChange (api, {payload}) {
  let pathname = payload && payload.location && payload.location.pathname

  if (_.includes(pathname, 'mine')) {
    let status = yield select((state) => state.dexAccount.state)
    if (status !== ENUM_ACCOUNT_STATUS.LOGGED_IN.getValue()) {
      // 直接在地址栏输入受保护的路由，不waitFor的话，put(push('/'))会失效。
      // 表现为地址栏已变化，但是页面并未正常刷新。
      // 可能是 router 尚未加载（appstate.loading控制着app.js中的路由组件，尚未渲染）。也可能是 store/index.js中的 history 中间件未加载完。
      // 优化点：在受保护组件内判断，就不会有一个短暂的切换。

      yield call(utils.waitFor, 200)
      console.info(`未登录，跳转主页, 状态：${ENUM_ACCOUNT_STATUS.toText(status)}`, )
      notification.success({ message: '请先登录' })
      yield put(push('/'))
    }
  }
}

export default function * root () {
  yield all([   
    takeLatest('@@router/LOCATION_CHANGE', safeWrap(onLocationChange), api),
    takeLatest('persist/REHYDRATE', safeWrap(startup), api)
  ])
}
