import moment from 'moment'
export default {
  state: {
    ready: false,
    loading: false,
    awardInfo: {
      visible: false,
      id: null,
      title: '恭喜您获得彩蛋',
      amount: 500,
      unit: 'LRC',
      createdAt: undefined
    }
  },
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
    startup: (state) => {
      return { 
        ...state, 
        ready: true 
      }
    },
    showEggAward: (state, payload) => {
      return { 
        ...state, 
        awardInfo: {
          visible: true,
          ...payload
        }
      }
    },
    closeAward: (state) => {
      let {awardInfo} = state
      awardInfo.visible = false
      return { 
        ...state, 
        awardInfo
      }
    }
  }
}