import _ from 'lodash'
import { Account, config, exchange } from 'librarys/lightcone_v3';
import { notification } from 'antd';
import utils from 'utils'
import api from 'api'
const BigNumber = require('bignumber.js');

const initState = {
  buyPrice: null,
  buyAmount: null,
  buyTotal: null,

  sellPrice: null,
  sellAmount: null,
  sellTotal: null,

  // 市价买入的”交易额“
  marketBuyTotal: null,
  // 市价卖出的”数量“
  marketSellAmount: null,
}

async function placeAnOrder (isBuy, tokenS, tokenB, amountS, amountB, dexAccount) {
  console.info({tokenS, tokenB, amountS, amountB, dexAccount})
  let owner = window.wallet.account
  let { accountId, passwordHash, apiKey } = dexAccount

  let exchangeAccount = new Account(window.wallet);
  const keyPair = exchangeAccount.generateKeyPair(passwordHash);
  let { publicKeyX, publicKeyY, secretKey: tradingPrivKey } = keyPair

  const tokenSConfig = config.getTokenBySymbol(tokenS)
  const orderId = await api.getOrderId(accountId, tokenSConfig.id, apiKey);
  
  // Timestamp in second
  const validSince = new Date().getTime() / 1000 - 10;
  const validUntil = new Date().getTime() / 1000 + 3600 * 24 * 30;

  console.info(`下单:`, {amountB, amountS})

  const signedEtheruemTx = exchange.submitOrder(window.wallet, {
    owner,
    accountId,
    keyPair: {
      publicKeyX,
      publicKeyY,
      secretKey: tradingPrivKey,
    },
    tokenS,
    tokenB,
    amountS,
    amountB,
    orderId,
    validSince: Math.floor(validSince),
    validUntil: Math.floor(validUntil),
    buy: isBuy
  })
  signedEtheruemTx.channelId = `wedex::subchannel::${_.random(1, 5)}`

  console.log('Order signed tx: ', signedEtheruemTx);
  const response = await api.submitOrderToLightcone(signedEtheruemTx, apiKey);
  console.log('Response of submitOrder: ', response);

  if (!response) return
  notification['success']({
    duration: 5,
    message: '下单成功'
  });
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload })
  },
  effects: dispatch => ({
    async placeBuyOrder (payload, { markets, dexAccount, placeOrder }) {
      let { baseToken: tokenB, quoteToken: tokenS } = markets.currentMarket
      let { buyAmount, buyPrice } = placeOrder
      if (!buyAmount || !buyPrice) return

      buyAmount = new BigNumber(buyAmount);
      let amountS = buyAmount.times(buyPrice).toFixed();
      let amountB = buyAmount.toFixed();

      let result = utils.withLoading(() => placeAnOrder(true, tokenS, tokenB, amountS, amountB, dexAccount))
      return result
    },
    async placeSellOrder (payload, { markets, dexAccount, placeOrder }) {
      let { baseToken: tokenS, quoteToken: tokenB } = markets.currentMarket
      let { sellAmount, sellPrice } = placeOrder
      if (!sellAmount || !sellPrice) return

      sellAmount = new BigNumber(sellAmount);
      let amountB = sellAmount.times(sellPrice).toFixed();
      let amountS = sellAmount.toFixed();

      let result = utils.withLoading(() => placeAnOrder(false, tokenS, tokenB, amountS, amountB, dexAccount))
      return result
    },
  })
}